import React, { FC, useEffect, useCallback, useRef} from 'react';
import styles from './styles/Style.module.scss'
import {ImageStore} from "../../store/stores/ImageStore";
import {observer} from "mobx-react-lite";

let IsShowingVideoInterface = false;

export const ImageWindow: FC = observer(() => {

    const ToOnMouseMove_CB = useCallback( () => {
        (document.getElementById('Image') as HTMLDivElement).style.cursor = 'none';
        (cross.current as HTMLDivElement).style.display = 'none';
        IsShowingVideoInterface = false;
    }, []);
    const cross = useRef<HTMLDivElement>(null);

    let ToOnMouseMove: Function;

    const onMouseMove = useCallback( () => {
        if (!IsShowingVideoInterface) {
            (document.getElementById('Image') as HTMLDivElement).style.cursor = 'pointer';
            (cross.current as HTMLDivElement).style.display = 'block';
        }
        IsShowingVideoInterface = true;
        // @ts-ignore
        clearTimeout(ToOnMouseMove);// @ts-ignore
        ToOnMouseMove = setTimeout(ToOnMouseMove_CB, 2000);
    }, [IsShowingVideoInterface]);

    useEffect (() => {
        if (ImageStore.isOpen) {
            IsShowingVideoInterface = true;
            (document.getElementById('Image') as HTMLDivElement).requestFullscreen();
            (document.getElementById('Image') as HTMLDivElement).addEventListener('fullscreenchange', () => {
                if (ImageStore.isOpen && document.fullscreenElement?.id !== 'Image') {
                    ImageStore.close();
                }
            });
        }
    });
    //TODO cross will have animation on top
    return (
        <>
            {ImageStore.isOpen
                ?
                    <div id={'Image'} className={styles.ImageWindow} onMouseMove={onMouseMove}>
                        <div className={styles.cross} ref={cross} onClick={() => {document.exitFullscreen()} }>
                            ✖
                        </div>
                        <img src={ImageStore.src} alt={''}/>
                    </div>
                :
                    <></>
            }
        </>
    );
});