import React from "react";
import classes from "./../../../styles/HeaderAndMusicPanelStyles.module.css";
import {MusicStore} from "../../store/stores/MusicStore";
import {observer} from "mobx-react-lite";

const ButtonRepeat = observer(() => {

    function EnableRepeat(){
        MusicStore.setRepeated(true);
        document.getElementById("SongInSiteAudio").loop = true;
    }
    function DisableRepeat(){
        MusicStore.setRepeated(false);
        document.getElementById("SongInSiteAudio").loop = false;
    }

    if (!MusicStore.isRepeated) {
        document.getElementById("SongInSiteAudio").loop = false;
    }

    return (
        <div className={classes.BlockToButtonRepeat}>
            <div className={classes.ButtonRepeatSong}>
                {!MusicStore.isRepeated
                    ?
                        <div className={classes.imgButtonRepeat} onClick={EnableRepeat}>

                            <svg x="0px" y="0px" width="30px" height="24px" viewBox="0 0 430 430">
                                <g>
                                    <g>
                                        <path d="M296.924,403.243c-22.656,10.875-47.5,17.813-73.734,19.813v-64.813c14.609-1.625,28.672-4.906,41.609-10.594
                                    L296.924,403.243z" fill={'#ffffff'}/>
                                        <path d="M64.814,199.868H0.001c8.219-107.219,97.906-192,207.188-192c64.297,0,124.735,30.094,163.797,79.969l59.812-34.563
                                    l0.125,197.844l-171.406-98.813l55.094-31.875c-27.062-30.313-65.999-48.563-107.421-48.563
                                    C133.251,71.868,72.876,128.055,64.814,199.868z" fill={'#ffffff'}/>
                                        <path d="M322.674,301.024l56,32.312c-14.531,21.156-32.875,39.47-54,54.031l-32.314-56
                                    C303.924,322.805,314.111,312.586,322.674,301.024z" fill={'#ffffff'}/>
                                        <path d="M191.189,358.243v64.813c-26.25-2-51.094-8.938-73.719-19.813l32.094-55.594
                                    C162.533,353.336,176.595,356.618,191.189,358.243z" fill={'#ffffff'}/>
                                        <path d="M122.033,331.368l-32.313,56c-21.156-14.562-39.5-32.875-54.031-54.031l56-32.312
                                    C100.251,312.586,110.439,322.805,122.033,331.368z" fill={'#ffffff'}/>
                                        <path
                                            d="M75.376,273.461l-55.563,32.094C8.938,282.93,2,258.118,0,231.868h64.813C66.439,246.461,69.72,260.524,75.376,273.461z"
                                        fill={'#ffffff'}/>
                                    </g>
                                </g>
                            </svg>
                        </div>
                    :
                        <div className={classes.imgButtonRepeat} onClick={DisableRepeat}>

                            <svg x="0px" y="0px" width="30px" height="24px" viewBox="0 0 430 430">
                                <g>
                                    <g>
                                        <path d="M296.924,403.243c-22.656,10.875-47.5,17.813-73.734,19.813v-64.813c14.609-1.625,28.672-4.906,41.609-10.594
                                    L296.924,403.243z" fill={'#6495ed'}/>
                                        <path d="M64.814,199.868H0.001c8.219-107.219,97.906-192,207.188-192c64.297,0,124.735,30.094,163.797,79.969l59.812-34.563
                                    l0.125,197.844l-171.406-98.813l55.094-31.875c-27.062-30.313-65.999-48.563-107.421-48.563
                                    C133.251,71.868,72.876,128.055,64.814,199.868z" fill={'#6495ed'}/>
                                        <path d="M322.674,301.024l56,32.312c-14.531,21.156-32.875,39.47-54,54.031l-32.314-56
                                    C303.924,322.805,314.111,312.586,322.674,301.024z" fill={'#6495ed'}/>
                                        <path d="M191.189,358.243v64.813c-26.25-2-51.094-8.938-73.719-19.813l32.094-55.594
                                    C162.533,353.336,176.595,356.618,191.189,358.243z" fill={'#6495ed'}/>
                                        <path d="M122.033,331.368l-32.313,56c-21.156-14.562-39.5-32.875-54.031-54.031l56-32.312
                                    C100.251,312.586,110.439,322.805,122.033,331.368z" fill={'#6495ed'}/>
                                        <path
                                            d="M75.376,273.461l-55.563,32.094C8.938,282.93,2,258.118,0,231.868h64.813C66.439,246.461,69.72,260.524,75.376,273.461z"
                                        fill={'#6495ed'}/>
                                    </g>
                                </g>
                            </svg>
                        </div>
                }
            </div>
        </div>
    );
})

export default ButtonRepeat;