import React, {useEffect, useState, memo, useRef, FC} from "react";
import MusicSearchLine from "./AI/MusicSearchLine";
import MusicLine from "./AI/MusicLine";
import GlobalStyles from '../../LayOut/styles/GlobalStyles.module.scss';
import classes from "./../../styles/HeaderAndMusicPanelStyles.module.css";
import {MusicStore} from "../store/stores/MusicStore";

let VariableSearchFrom/*:number Искать с какой позиции, нужно для бесконечной музыки */ = 0;

const MusicList:FC<{FunctionHide: any}> = memo((probs) => {

    const [MusicLines, SetMusicLines] = useState<undefined | any>(MusicStore.array === null ? undefined : MusicStore.array),
        LastMusic = useRef<HTMLDivElement>(null),
        observer = useRef<IntersectionObserver>();
    let VariableToTimeOutSearchMusic:Function, IsMusicLoading/*:boolean Загружаем ли музыку */ = false, IsThereMore = 1;

    function SearchMusic(SearchFrom = 0){
        try{
            if ((document.getElementById('MusicSearchLineInput') as HTMLInputElement).value !== ''){
                IsMusicLoading = true;
                if (!(document.getElementById('MusicSearchLineInput') as HTMLInputElement).value
                    || (document.getElementById('MusicSearchLineInput') as HTMLInputElement).value === '') {
                    return;
                }
                fetch (`http://localhost:3001/user/Music/GetMusic/?SearchFrom=${SearchFrom}` +
                    `&SearchTitle=${(document.getElementById('MusicSearchLineInput') as HTMLInputElement).value.toLowerCase()}`, {
                    method:"GET",
                    credentials: 'include',
                }).then(
                    response => {
                        return response.json();
                    },
                ).then(
                    Response => {
                        IsMusicLoading = false;
                        if (Response.status === 404) {
                            SetMusicLines(undefined);
                        } else {
                            SetMusicLines(Response);
                        }
                        VariableSearchFrom = 0;
                        MusicStore.addSongs(Response)
                        IsThereMore = 1;
                    }
                )
            }
        } catch (error) {}
            
    }

    function LikeSearchMusic () {
        // @ts-ignore
        clearTimeout(VariableToTimeOutSearchMusic);// @ts-ignore
        VariableToTimeOutSearchMusic = setTimeout(SearchMusic, 2000);
    }

    useEffect (() => {
        if (IsMusicLoading) {
            return;
        }
        if (observer.current) {
            observer.current.disconnect();
        }
        const callback = function (entries: any) {
            if (IsThereMore){
                if (entries[0].isIntersecting) {
                    VariableSearchFrom += 1;
                    IsMusicLoading = true;
                    if (!(document.getElementById('MusicSearchLineInput') as HTMLInputElement).value
                        || (document.getElementById('MusicSearchLineInput') as HTMLInputElement).value === '') {
                        return;
                    }
                    fetch (`http://localhost:3001/user/Music/GetMusic/?SearchFrom=${VariableSearchFrom}`+
                    `&SearchTitle=${(document.getElementById('MusicSearchLineInput') as HTMLInputElement).value}`, {
                        method:"GET",
                        credentials: 'include',
                    }).then (
                        response => {
                            return response.json();
                        }
                    ).then (
                        Response => {
                            try {
                                IsMusicLoading = false;
                                if (Response['array'].length === 0 || Response.status === 404) {IsThereMore = 0;}
                                else {
                                    let NewMusicLines: any /*:object[] Эта переменная хранит новое значение MusicLines */ = {};
                                    NewMusicLines['count'] = Response['count'];
                                    // @ts-ignore
                                    NewMusicLines['array'] = MusicLines['array'];
                                    NewMusicLines['IsThereMore'] = Response['IsThereMore'];
                                    Response['array'].forEach( (element:any) => {
                                        NewMusicLines['array'].push(element);
                                    });
                                    MusicStore.addSongs(NewMusicLines);
                                    SetMusicLines(NewMusicLines);
                                }
                            } catch(error) {}
                        }
                    )
                }
            }
        }
        if (LastMusic.current) {
            observer.current = new IntersectionObserver(callback);
            observer.current.observe(LastMusic.current);
        }
    }, [MusicLines, IsThereMore]);

    return (
        //Div создан для того, чтобы поместить в него 3 самостоятельных элемента
        <div>
            <div className={GlobalStyles.ModalWindow} onClick={probs.FunctionHide}></div>
            <div className={classes.Music} id="Music">
                <MusicSearchLine
                    FSearchMusic/*Функция SearchMusic */ = {LikeSearchMusic}
                ></MusicSearchLine>
                {MusicLines === undefined || MusicLines['count'] === undefined || MusicLines['count'] === 0
                    ?
                        <div> 
                            <div style={{width:"100px", position:"relative", top:"40px", left:"237px"}}>

                                <svg height="128px" width='128px' viewBox="0 0 64 64">
                                    <path
                                        d="M23.727,16.403v4.84v5.058v14.236c-1.268-0.777-2.754-1.233-4.35-1.233c-4.612,0-8.353,3.74-8.353,8.349  c0,4.614,3.74,8.354,8.353,8.354c4.61,0,8.349-3.739,8.35-8.352h0V25.151l21.25-6.109V33.33c-1.268-0.777-2.754-1.233-4.35-1.233  c-4.614,0-8.353,3.739-8.353,8.348c0,4.613,3.739,8.354,8.353,8.354c4.344,0,7.914-3.325,8.31-7.57h0.04V17.892v-3.586V7.993  L23.727,16.403z"/>
                                </svg>
                            </div>
                            <div className={classes.EmptySearchResult}>
                                По вашему запросу ничего не найдено
                            </div>
                        </div>  

                    :
                        <div>
                            <div className={classes.MusicListComponent}>
                            {
                                MusicLines['array'].map((MusicLines: any, index: number) =>
                                    <MusicLine
                                        MusicLineData={MusicLines}
                                        counter={index + 1}
                                        key={index + 1}/>
                                )
                            }
                            </div> 
                        </div>
                }
            <div ref={LastMusic} style={{height: "0px"}}></div>
            </div>
        </div>
    );
})
export default MusicList;