import React, {FC, useCallback, useEffect, useMemo, useState} from 'react';
import {observer} from "mobx-react-lite";
import {ChatsStore} from "../../store/ChatsStore";
import {binarySearchObjectSync, binarySearchSync} from "../../helper/binaryOperation";
import {UsersStore} from "../../store/UsersStore";
import {LazyImg} from "../../helper/LazyImg";
import styles from  '../../styles/MessageBLock.module.scss'
import { Loading } from '../../helper/Loading';
import StarIcon from '@mui/icons-material/Star';
import EditIcon from '@mui/icons-material/Edit';
import {EditMenu} from "./EditMenu";
import {GetAvatarSrc} from "../../functions/GetAvatarSrc";
import {USERID} from "../../index";

export const InterlocutorPanel:FC = observer(() => {
    const index = binarySearchObjectSync(ChatsStore.chats, ChatsStore.whoIsActive, 'id') as number
    const Chat = useMemo(() => ChatsStore.chats[index], [ChatsStore.whoIsActive, ChatsStore.chats[index]])
    const [isFavourite, setIsFavourite] = useState(ChatsStore.chatsList[0].chats.indexOf(ChatsStore.whoIsActive) !== -1)
    const [isStarIconOnHover, setIsStarIconOnHover] = useState(false);
    const [isEditIconOnHover, setIsEditIconOnHover] = useState(false);
    const [isEditingWindowOpen, setIsEditingWindowOpen] = useState(false);
    const isOnline = (() => {
        if (Chat.members.length > 2) return false
        if (Chat.members[0] == +USERID) return binarySearchSync(UsersStore.onlineUsers, UsersStore.onlineUsers[1], true).found;
        else return binarySearchSync(UsersStore.onlineUsers, UsersStore.onlineUsers[0], true).found;
    })(),
        avatarSrc: string = useMemo(() => GetAvatarSrc(Chat), [ChatsStore.whoIsActive, UsersStore.users.length]);
    
    useEffect(() => {
        setIsFavourite(ChatsStore.chatsList[0].chats.indexOf(ChatsStore.whoIsActive) !== -1);
        setIsEditingWindowOpen(false);
    }, [ChatsStore.whoIsActive]);
    const SwitchFavourites = useCallback(() => {
        setIsFavourite(!isFavourite);
        const newArray = [...ChatsStore.chatsList[0].chats]
        if (isFavourite) {
            newArray.splice(newArray.indexOf(ChatsStore.whoIsActive, 1));
            ChatsStore.ChangeChatsInChatList('favourites', newArray);
        } else {
            newArray.push(ChatsStore.whoIsActive);
            ChatsStore.ChangeChatsInChatList('favourites', newArray);
        }
    }, [isFavourite, ChatsStore.whoIsActive]);

    const SetIsEditingWindowOpen = useCallback((value: boolean) => {
        setIsEditingWindowOpen(value);
    }, [])

    return (
        <div className={"interlocutor"} style={{left: '0', top: '0', fontSize: '20px', width: '500px', paddingLeft: '20px'}}>
            {Chat.name}
            <div style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                <div>
                    {isEditingWindowOpen
                        ?
                            <>
                                <div style={{position: 'fixed', width: '100vw', height: '100vh', zIndex: '999', top: '0', left: '0', backgroundColor: 'rgba(0, 0, 0, .1)'}}
                                  onClick={() => {setIsEditingWindowOpen(false)}}/>
                                <div style={{position: 'absolute', top: '0', height: 'calc(100vh - 55px)', zIndex: '1000', width: '520px', left: '0', overflow: 'hidden',
                                    backgroundColor: '#e3f3ff', display: 'flex', justifyContent: 'space-between'}}>
                                    <EditMenu setIsEditingWindowOpen={SetIsEditingWindowOpen} Chat={Chat}/>
                                </div>
                            </>
                        : <></>
                    }
                    {Chat.isConversation
                        &&
                            <EditIcon
                                style={{color: 'rgba(0, 0, 0, .3)', marginRight: '10px', padding: '5px', cursor: 'pointer'}}
                                onMouseEnter={() => {setIsEditIconOnHover(true)}} className={isEditIconOnHover ? styles.hover : ''}
                                onClick={() => {setIsEditingWindowOpen(!isEditingWindowOpen)}} onMouseLeave={() => {setIsEditIconOnHover(false)}}
                            />
                    }
                </div>
                <div>
                    <StarIcon style={{color: isFavourite ? '#ffe066' : 'rgba(0, 0, 0, .3)', marginRight: '10px', padding: '5px', cursor: 'pointer'}}
                              onMouseEnter={() => {setIsStarIconOnHover(true)}} className={isStarIconOnHover ? styles.hover : ''}
                              onClick={SwitchFavourites} onMouseLeave={() => {setIsStarIconOnHover(false)}}
                    />
                </div>
                <LazyImg src={avatarSrc} ImageStyles={isOnline ? {width: '38px', height: '40px', borderRadius: '50%'} : {width: '40px', height: '40px', borderRadius: '50%'}}
                         DivToImageStyles={isOnline ? {marginRight: '3px', border: 'solid rgb(75, 179, 115) 2px', borderRadius: '50%', width: '38px', height: '38px'} : {marginRight: '3px'}}
                         element={<Loading/>}/>
            </div>
        </div>
    );
});