import React, {memo, FC, useRef, useCallback, useEffect} from 'react';
import styles from '../Styles/style.module.scss'
import {IsVideoPlaying, PauseVideo, PlayVideo} from "./UI/BottomController";
import {VideoStore} from "../../../store/stores/VideoStore";

export const Video: FC = memo (() => {

    const videoRef = useRef<HTMLVideoElement> (null);

    const OnLoad = useCallback (() => {
        if (videoRef.current !== null) {
            videoRef.current.style.display = 'block';
            (document.getElementById('VideoPlayer') as HTMLDivElement).requestFullscreen();
        }
    }, []);

    useEffect (() => {
        (document.getElementById('VideoPlayer') as HTMLDivElement).addEventListener('fullscreenchange', () => {
            if (VideoStore.isOpen && document.fullscreenElement?.id !== 'VideoPlayer') {
                VideoStore.close(
                    (videoRef.current as HTMLVideoElement).currentTime,
                    (videoRef.current as HTMLVideoElement).playbackRate,
                    (videoRef.current as HTMLVideoElement).volume
                )
            }
        })
    }, []);

    const onClick = () => {
        IsVideoPlaying ? PauseVideo() : PlayVideo()
    }

    return (
        <div className={styles.VideoBlock}>
            <video onClick={onClick} className={styles.Video} src={VideoStore.src} id={'Video'} ref={videoRef} onCanPlay={OnLoad} controls={false}></video>
        </div>
    );

});