import React, {memo, FC, forwardRef, useState, useEffect, useCallback} from 'react';
import {RawInput} from "./rawInput";
import ArrowDownwardIcon from "@mui/icons-material/ArrowDownward";
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';

export const CheckText = (value: string, defaultValue: string, length: number, reg: 'all' | 'eng' | 'number' | 'strictNumber' | 'eng_AND_rus' | {reg: RegExp},
    checkSpacing: boolean = false): string => {
    let newValue = value;

    if (newValue.length > length) {newValue = newValue.slice(0, length)}
    if (reg !== 'all') {
        switch (reg) {
            case'eng':{newValue = newValue.replace (/[^A-Za-z\d\s]+/g, '');break;}
            case "number":{newValue = newValue.replace (/[^\d\s]+/g, '');break;}
            case "strictNumber":{newValue = newValue.replace (/\D/g, '');break;}
            case "eng_AND_rus":{newValue = newValue.replace (/[^A-Za-zа-яА-Я\d\s]+/g, '');break;}
            default: {
                if (reg.reg) {
                    newValue = newValue.replace (reg.reg, '');
                }
            }
        }
    }
    if (checkSpacing) {
        const Check = () => {
            if (newValue.slice(0, 1) === ' ' || newValue.slice(0, 1) === ' ') {
                newValue = newValue.substring(1, newValue.length);
                Check();
            }
            if (newValue.slice(newValue.length - 1, newValue.length) === ' ' || newValue.slice(newValue.length - 1, newValue.length) === ' ') {
                newValue = newValue.substring( newValue.length - 1, 0);
                Check();
            }
        }
        Check();
    }
    if (newValue === '') return defaultValue;
    return newValue;
}

//@ts-ignore
export const AlreadyInput = memo(forwardRef<HTMLInputElement, {label?: string;placeholder?:string;ClassName?: string;
    Style?: React.CSSProperties;width?: string; onChange?: any;colorOfActive?: string;onEnter?: any;
    length: number;reg: 'all' | 'eng' | 'number' | 'strictNumber' | 'eng_AND_rus' | {reg: RegExp};onClick?: () => void;
    crossStyles?: React.CSSProperties;value?: string
    ref?: any;design: 'default' | 'cross' | 'arrowDown' | 'arrowUp'}>(({label, placeholder,
        length, reg, Style, ClassName, design, onChange, value,onEnter,
        crossStyles, width, onClick, colorOfActive}, ref) => {

    const [Value, setValue] = useState(value);

    const Enter = useCallback(() => {
        onEnter();
        setValue('');
    },[])

    const SetValue = useCallback((value: string) => {
        setValue(value);
    }, []);

    if (design === 'cross' && onClick == undefined) {
        onClick = () => {
            setValue('');
        };
    }

    return (
        <>
            {design === 'default'
                ?
                    <RawInput SetValue={SetValue} onEnter={Enter} value={Value} onChange={onChange} colorOfActive={colorOfActive} length={length} className={ClassName} placeholder={placeholder} label={label} style={{...Style, fontFamily: 'inherit', width: width}} ref={ref} reg={reg}/>
                :
            design === 'cross'
                ?
                    <>
                        <RawInput SetValue={SetValue} onEnter={Enter} value={Value} onChange={onChange} colorOfActive={colorOfActive} length={length} className={ClassName} placeholder={placeholder} label={label} style={{...Style, fontFamily: 'inherit', paddingRight: '40px', width: width}} ref={ref} reg={reg}/>
                        <div style={{position: 'relative'}} onClick={onClick}>
                            <div
                                style={{
                                    position: 'absolute',
                                    color: 'red',
                                    fontSize: '40px',
                                    cursor: 'pointer',
                                    top: '-61px',
                                    left: 'calc(' + width + ' + 18px)',
                                    ...crossStyles
                                }}
                            >
                                ×
                            </div>
                        </div>
                    </>
                :
            design === 'arrowDown'
                ?
                    <>
                        <RawInput SetValue={SetValue} onEnter={Enter} value={Value} onChange={onChange} colorOfActive={colorOfActive} length={length} className={ClassName} placeholder={placeholder} label={label} style={{...Style, fontFamily: 'inherit', paddingRight: '40px', width: width}} ref={ref} reg={reg}/>
                        <ArrowDownwardIcon
                            onClick={Enter}
                            style={{
                                position: 'absolute',
                                fontSize: '30px',
                                fill: 'grey',
                                top: '14px',
                                right: '5px',
                                cursor: 'pointer',
                                left: 'calc(' + width + ' + 18px)',
                                ...crossStyles
                            }}
                        />
                    </>
                :
            design === 'arrowUp'
                ?
                    <>
                        <RawInput SetValue={SetValue} onEnter={Enter} value={Value} onChange={onChange} colorOfActive={colorOfActive} length={length} className={ClassName} placeholder={placeholder} label={label} style={{...Style, fontFamily: 'inherit', paddingRight: '40px', width: width}} ref={ref} reg={reg}/>
                        <ArrowUpwardIcon
                            onClick={Enter}
                            style={{
                                position: 'absolute',
                                fontSize: '30px',
                                fill: 'grey',
                                cursor: 'pointer',
                                top: '10px',
                                right: '5px',
                                left: 'calc(' + width + ' + 18px)',
                                ...crossStyles
                            }}
                        />
                    </>
                :
                    <></>
            }
        </>

    );

}));