import React, {FC, memo} from "react";
import classes from "../../../styles/HeaderAndMusicPanelStyles.module.css";


const InfiniteInput:FC<{FSearchMusic: any, Pplaceholder: string, id: string}> = memo((probs) => {

    function OnSubmit(e: any){
        e.preventDefault();
        probs.FSearchMusic()
    }

    return (
        <div className={classes.InfiniteInput}>
            <input 
             onChange={probs.FSearchMusic}
             onSubmit={OnSubmit}
             placeholder={probs.Pplaceholder}
             style={{outline: "none", border: "none", height: "29px", position: "relative", width: "24000px", fontSize:"20px", padding: '0 0 0 8px', top:"6px"}}
             id={probs.id}
            />
        </div>
    );
})

export default InfiniteInput;