import React, {memo, FC} from 'react';

interface Props {
    styles?: React.CSSProperties;
}
/**
 *Для корректной работы стоит подключить SetHTML.css
 * */
export const SetLoading: FC<Props> = memo(({styles = {}}) => {

    return (
        <div style={{height:"100%"}}>
            <div style={styles} className={"loading"}>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
    );

});