import React, {memo, FC, useCallback} from 'react';
import {DOMAIN} from "../../helper/api";
import {USERID} from "../../index";
import {SetNotification} from "../../helper/SetNotification";

const LeftMenu: FC = memo(() => {

    const openProfile = useCallback(() => {
        if (USERID) {
            document.location.href = DOMAIN + "/pages/profile/?userId=" + USERID;
        } else {
            SetNotification("Для просмотра своего профиля необходимо войти в аккаунт");
        }
    }, []);

    const openMessenger = useCallback(() => {
        if (USERID) {
            document.location.href = DOMAIN + "/pages/messenger/";
        } else {
            SetNotification("Для просмотра сообщений необходимо войти в аккаунт");
        }
    }, []);

    const openFriends = useCallback(() => {
        if (USERID) {
            document.location.href = DOMAIN + "/pages/friends/";
        } else {
            SetNotification("Для просмотра своих друзей необходимо войти в аккаунт");
        }
    }, []);

    return (
        <div className={"left_menu"} style={{display: "inline-block"}}>
            <a className="button_profile1">
                <div className="button_profile" onClick={openProfile}>Мой профиль</div>
            </a>
            <a className="button_message1">
                <div className="button_message" onClick={openMessenger}>Сообщения</div>
            </a>
            <a className="button_friends1">
                <div className="button_friends" onClick={openFriends}>Друзья</div>
            </a>
        </div>
    );

});

export default LeftMenu;
