import {DOMAIN} from "../helper/api";
import {UsersStore} from "../store/UsersStore";
import {Chat} from '../store/ChatsStore'
import {USERID} from "../index";

export const GetAvatarSrc = (Chat: Chat) => {
    if (Chat.isConversation) {
        if (Chat.avatar == null || Chat.avatar == '') {
            return DOMAIN + '/UserFiles/NULL.png';
        } else {
            return DOMAIN + '/UserFiles/' + Chat.members[0] + '/Image/' + Chat.avatar;
        }
    }
    let userAvatar;
    if (Chat.members[0] == +USERID) {
        userAvatar = UsersStore.users.find(User => User.ID == Chat.members[1]).Avatar;
        if (userAvatar == null || userAvatar == "") {
            return DOMAIN + '/UserFiles/NULL.png';
        } else {
            return DOMAIN + '/UserFiles/' + Chat.members[1] + '/Image/' + userAvatar;
        }
    } else {
        userAvatar = UsersStore.users.find(User => User.ID == Chat.members[0]).Avatar;
        if (userAvatar == '' || userAvatar == null) {
            return DOMAIN + '/UserFiles/NULL.png';
        } else {
            return DOMAIN + '/UserFiles/' + Chat.members[0] + '/Image/' + userAvatar;
        }
    }
}