import React, {FC} from 'react';
import {MessageBox} from "./MessageBox";
import {InterlocutorPanel} from "./InterlocutorPanel";
import {MessageInput} from "./UI/MessageInput";
import {observer} from "mobx-react-lite";
import {ChatsStore} from "../../store/ChatsStore";
import ForumIcon from '@mui/icons-material/Forum';

export const MessageBlock: FC = observer(() => {

    if (ChatsStore.whoIsActive === -1) {
        return (
            <div className={'LeftColumn'} style={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
               <div style={{fontSize: '24px', flexFlow: 'column', display: 'flex', alignItems: 'center'}}>
                   <div style={{marginBottom: '15px'}}>Выберете чат или создайте новый</div>
                   <ForumIcon style={{color: 'rgba(0, 0, 0, .1)', fontSize: '80px'}}/>
               </div>
            </div>
        )
    }
    
    return (
        <div className={'LeftColumn'} style={{display: 'flex', padding: '0', width: '520px', flexFlow: 'column'}}>
            <InterlocutorPanel/>
            <MessageBox/>
            <MessageInput/>
        </div>
    );

});