import React, {memo, FC, useCallback} from 'react';
import {AlreadyInput} from "../../helper/AlreadyInput";
import {Chat, ChatsStore} from "../../store/ChatsStore";

interface Props {
    setFoundArray: (chats: Chat[]) => void
}

export const SearchLine: FC<Props> = memo(({setFoundArray}) => {
    let callback: any;
    const find = useCallback((e: any) => {
        const value = e.target.value, array:Chat[] = [];
        if (value === '') {
            setFoundArray([]);
            return;
        }
        clearTimeout(callback);
        callback = setTimeout(() => {
            for (let i = 0; i < ChatsStore.chats.length; i++) {
                if ((ChatsStore.chats[i].name as string).toLowerCase().indexOf(value) != -1) array.push(ChatsStore.chats[i])
            }
            setFoundArray([...array]);
        }, 1500)
    }, []);
    return (
        <div style={{display: 'flex', width: '269px'}}>
            <AlreadyInput placeholder={'Найти нужный чат'} design={'default'} onChange={find} onEnter={find} width={'233px'} Style={{height: '32px', top: '4px', position: 'relative', left: '2px'}} length={120} reg={'all'}/>
            <svg style={{fill: 'rgba(0, 0, 0, .1)',width: '32px'}}
                 viewBox="0 0 24 24" data-testid="SearchIcon"><path d="M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z"></path></svg>
        </div>
    );
});