import React, {memo, FC, useState, useRef} from 'react';
import {Slider} from "@mui/material";
import {VideoStore} from "../../../../store/stores/VideoStore";
import {observer} from "mobx-react-lite";

export const VideoVolume: FC = observer(() => {

    const [Volume, setVolume] = useState (VideoStore.preferVolume * 100);
    const VideoVolumeSlider = useRef(null);

    const ChangeVolume = (e: Event, newValue: number | number[]) => {
        // @ts-ignore
        (document.getElementById('Video') as HTMLVideoElement).volume = newValue / 100;
        // @ts-ignore
        setVolume(newValue);
    }

    return (
        <div style={{display: 'inline-block', left: '10px', position: 'relative'}}
             onMouseEnter={() => { // @ts-ignore
                 VideoVolumeSlider.current.style.display = ' inline-block'}}
             onMouseLeave={() => { // @ts-ignore
                 VideoVolumeSlider.current.style.display = ' none'}}
        >
            {Volume > 0
                ?
                    <svg onClick={() => {
                        setVolume(0);
                        (document.getElementById('Video') as HTMLVideoElement).volume = 0;
                    } } style={{position: 'relative', top: '0'}} width="25" height="25" viewBox="0 0 16 16">
                    <path fill={'#fff'} d="M12 8.02c0 1.09-.45 2.09-1.17 2.83l-.67-.67c.55-.56.89-1.31.89-2.16 0-.85-.34-1.61-.89-2.16l.67-.67A3.99 3.99 0 0 1 12 8.02zM7.72 2.28L4 6H2c-.55 0-1 .45-1 1v2c0 .55.45 1 1 1h2l3.72 3.72c.47.47 1.28.14 1.28-.53V2.81c0-.67-.81-1-1.28-.53zm5.94.08l-.67.67a6.996 6.996 0 0 1 2.06 4.98c0 1.94-.78 3.7-2.06 4.98l.67.67A7.973 7.973 0 0 0 16 8c0-2.22-.89-4.22-2.34-5.66v.02zm-1.41 1.41l-.69.67a5.05 5.05 0 0 1 1.48 3.58c0 1.39-.56 2.66-1.48 3.56l.69.67A5.97 5.97 0 0 0 14 8.02c0-1.65-.67-3.16-1.75-4.25z"/>
                </svg>
                :
                    <svg style={{position: 'relative', top: '0'}} width="25" height="25" viewBox="0 0 16 16"
                         onClick={() => {
                             setVolume(10);
                             (document.getElementById('Video') as HTMLVideoElement).volume = 0.10;
                         } }
                    >
                        <path fill={'#fff'} d="M8 2.81v10.38c0 .67-.81 1-1.28.53L3 10H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h2l3.72-3.72C7.19 1.81 8 2.14 8 2.81zm7.53 3.22l-1.06-1.06-1.97 1.97-1.97-1.97-1.06 1.06L11.44 8 9.47 9.97l1.06 1.06 1.97-1.97 1.97 1.97 1.06-1.06L13.56 8l1.97-1.97z"/>
                    </svg>
            }

            <Slider aria-label="Volume"
                    style={{width: '100px', color: 'white', display: 'none', left: '12px', top: '-1px', padding: '10px', height: '2px'}}
                    ref={VideoVolumeSlider}
                    onChange={ChangeVolume}
                    value={Volume}
            />
        </div>
    );

});