import React, {useState, memo} from "react";
import classes from "./../../../styles/HeaderAndMusicPanelStyles.module.css";
import {MusicStore} from "../../store/stores/MusicStore";

const VolumeSign = memo(() => {
    const [IsMute, SetMute] = useState(0),[IsOnHover, SetHover] = useState(0);
    let IsSoundON = ( MusicStore.lastVolume > 0 && IsMute !== 1) ? 1 : 0;
   

    const MouseOver = () => {
        setTimeout( () => {
            document.getElementById('MusicInSiteTopPanel1').style.width = "430px";
        })

        SetHover(1);
    }
    const MouseOut = () => {
        setTimeout( () => {
            document.getElementById('MusicInSiteTopPanel1').style.width = "340px";
        })
        SetHover(0);
    }
    const SetVolume = (e) => {
        e.stopPropagation();
        SetMute(0);
        MusicStore.setVolume(document.getElementById('MusicSiteVolumeRange').value / 100);
        try {
            document.getElementById('SongInSiteAudio').volume = (document.getElementById('MusicSiteVolumeRange').value) / 100;
        } catch (e) {}
    }

    const MuteSongInSite = (e) => {
        e.stopPropagation()
        document.getElementById('SongInSiteAudio').volume = 0;
        SetMute(1);
    }

    const UnMuteSongInSite = (e) => {
        e.stopPropagation()
        MusicStore.lastVolume > 0
            ?
                document.getElementById('SongInSiteAudio').volume = MusicStore.lastVolume
            :
                MusicStore.lastVolume = 0.01
                document.getElementById('SongInSiteAudio').volume = MusicStore.lastVolume
                
        SetMute(0);
    }
    return (
        <div className={classes.VolumeSignDiv} onMouseEnter={MouseOver} onMouseLeave={MouseOut} onMouseMove={MouseOver}>
            <div className={classes.VolumeSignImgDiv} style={{display:"inline-block"}}>
                {IsSoundON === 1
                    ?
                        <div onClick={MuteSongInSite} className={classes.VolumeSignImg}>
                            <svg width="30" height="24" viewBox="0 0 16 14">
                                <path
                                    d="M12 8.02c0 1.09-.45 2.09-1.17 2.83l-.67-.67c.55-.56.89-1.31.89-2.16 0-.85-.34-1.61-.89-2.16l.67-.67A3.99 3.99 0 0 1 12 8.02zM7.72 2.28L4 6H2c-.55 0-1 .45-1 1v2c0 .55.45 1 1 1h2l3.72 3.72c.47.47 1.28.14 1.28-.53V2.81c0-.67-.81-1-1.28-.53zm5.94.08l-.67.67a6.996 6.996 0 0 1 2.06 4.98c0 1.94-.78 3.7-2.06 4.98l.67.67A7.973 7.973 0 0 0 16 8c0-2.22-.89-4.22-2.34-5.66v.02zm-1.41 1.41l-.69.67a5.05 5.05 0 0 1 1.48 3.58c0 1.39-.56 2.66-1.48 3.56l.69.67A5.97 5.97 0 0 0 14 8.02c0-1.65-.67-3.16-1.75-4.25z"/>
                            </svg>
                        </div>
                    :
                        <div onClick={UnMuteSongInSite} className={classes.VolumeSignImg}>

                            <svg width="30" height="24" viewBox="0 0 16 14">
                                <path d="M8 2.81v10.38c0 .67-.81 1-1.28.53L3 10H1c-.55 0-1-.45-1-1V7c0-.55.45-1 1-1h2l3.72-3.72C7.19 1.81 8 2.14 8 2.81zm7.53 3.22l-1.06-1.06-1.97 1.97-1.97-1.97-1.06 1.06L11.44 8 9.47 9.97l1.06 1.06 1.97-1.97 1.97 1.97 1.06-1.06L13.56 8l1.97-1.97z"/>
                            </svg>
                        </div>
                }
            </div>
            {IsOnHover === 1
                ?
                    <div className={classes.VolumeSignRangeDiv} style={{display:"inline-block"}}
                     onClick={SetVolume}>
                        <input style={{display:"inline-block", width:"100px", height:"28px"}}
                        className={classes.VolumeSignRange}
                        min={"0"} max={"100"}
                        defaultValue={MusicStore.LastVolume * 100}
                        step={"1"} id="MusicSiteVolumeRange"
                        type={"range"} 
                        placeholder={""}
                        />
                    </div>
                :
                    ""
            }    
        </div>
    );
})

export default VolumeSign;