import React, {memo, FC, useState} from 'react';
import classes from "../../../styles/HeaderAndMusicPanelStyles.module.css";
import {DOMAIN} from "../../../helper/api";
import LogoutIcon from '@mui/icons-material/Logout';

const ExitButtonInHeader: FC = memo((probs ) => {

    const [IsExitWindow/*Существует ли сейчас окно выхода*/, SetExitWindow] = useState(0);

    function ShowExitWindow():void {
        SetExitWindow(1);
    }

    const FunctionExit = ():void => {
        document.location.href = DOMAIN + '/pages/registration/';
    }

    const HideExitWindow = (e: MouseEvent):void => {
        e.stopPropagation();
        SetExitWindow(0);
    }

    return (
        <div id="ExitButtonInHeader" className={classes.ExitButtonInHeader} onClick={ShowExitWindow}>
            <div className={classes.exit_btn}>
                <LogoutIcon style={{height: '35px', fill: '#fff', position: 'relative', left: '6px'}}/>
            </div>
            {IsExitWindow == 1
                ?
                    <div className={classes.exit_window}>
                        <div className={classes.d2}>
                            <div className={classes.d3}>Вы точно хотите выйти из аккаунта?</div>
                            <div className={classes.d4} onClick={FunctionExit}>
                                <div style={{top: "calc(50% - 21px)", position: "relative"}}>ДА</div>
                            </div>
                            {/* @ts-ignore */}
                            <div className={classes.d5} onClick={HideExitWindow}>
                                <div style={{position: "relative", top: "calc(50% - 21px)"}}>НЕТ</div>
                            </div>
                        </div>
                    </div>
                :
                    ''
            }
        </div>
    );

});

export default ExitButtonInHeader;