import React, {
    memo,
    FC,
    forwardRef,
    ChangeEvent,
    FocusEvent,
    useState,
    useCallback,
    KeyboardEventHandler,
    useEffect
} from 'react';
import {CheckText} from "./AlreadyInput";
import styles from "./Input.module.scss";

export const RawInput: FC<{length: number;reg: 'all' | 'eng' | 'number' | 'strictNumber' | 'eng_AND_rus' | {reg: RegExp};label?: string;colorOfActive?: string;onEnter?: any;
    placeholder?:string;className?: string;style?: React.CSSProperties;value?:string;onChange?: any; SetValue: (value: string) => void;
    ref: any}> = memo (forwardRef( ({length, reg, placeholder, label, className, style, onChange, colorOfActive = '#1976d2', value='', onEnter, SetValue}, ref) => {

    const onInputChange = useCallback((event: ChangeEvent<HTMLInputElement>) => {
        event.target.value = CheckText(event.target.value, '', length, reg, false);
        SetValue(event.target.value);
        if (onChange !== undefined) onChange(event);
    }, [value])
    const onBlur = useCallback((event: ChangeEvent<HTMLInputElement>) => {
        event.target.value = CheckText(event.target.value, '', length, reg, true);
        event.target.style.border = '';
    }, [])

    const onInputFocus = useCallback((event: FocusEvent<HTMLInputElement>) => {
        event.target.style.border = 'solid 2px ' + colorOfActive;
    }, [])

    const onKeyUp = useCallback((e: KeyboardEvent) => {
        if ( (e.key === 'Enter' || e.keyCode === 13) && onEnter != undefined) {
            onEnter();
        }
    }, []);

    return (
        <>
            {label !== undefined && value !== ''
                ?
                    <div className={styles.label}>
                        {label}
                    </div>
                :
                    <></>
            }
            {/*@ts-ignore*/}
            <input onBlur={onBlur} onKeyUp={onKeyUp} className={styles.Input + ' ' + className} style={style} ref={ref} onChange={onInputChange} value={value} placeholder={label ? label : placeholder} onFocus={onInputFocus}/>
        </>
    );

}));