import React, {
    memo,
    FC,
    useMemo,
    useState,
    useCallback,
    useEffect,
    useRef,
    KeyboardEventHandler,
    KeyboardEvent
} from 'react';
import styles from '../../../styles/MessageBLock.module.scss'
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import {UsersStore} from "../../../store/UsersStore";
import {binarySearchObjectSync} from "../../../helper/binaryOperation";
import {USERID} from "../../../index";
import {MessageStore} from "../../../store/MessageStore";
import {SetNotification} from "../../../helper/SetNotification";

interface Props {
    id: number;
    author: number;
    date: string;
    data: string;
    files: string[];
    avatar: Map<any, any>;
    //We compare the last author and this author with the current to correct print.
    lastAuthorIsIt: boolean;
}

export const Message:FC<Props> = memo(({author, date, data, files, avatar, lastAuthorIsIt, id}) => {

    const [isActive, setIsActive] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [messageText, setMessageText] = useState(data);
    const messageTextBlock = useRef<HTMLDivElement>(null);

    const toggleActive = useCallback(() => {
        setIsActive(!isActive);
    }, [isActive]);
    const [{index, found}, setUserIndex] = useState(binarySearchObjectSync(UsersStore.users, author, 'ID', true) as {index: number, found: boolean})
    const Author = useMemo(() => {
        if (lastAuthorIsIt) return '';
        if (!found) {
            (UsersStore.GetUsers([author.toString()]) as Promise<void>)
                .then(() => {
                    setUserIndex(binarySearchObjectSync(UsersStore.users, author, 'ID', true) as {index: number, found: boolean});
                })
            return 'Загрузка...';
        }
        return  UsersStore.users[index].Name + ' ' + UsersStore.users[index].Surname
    }, [UsersStore.users.length]);
    const deleteMessage = useCallback(() => {
        MessageStore.deleteMessage(id);
    }, [])
    const updateMessage = useCallback(() => {
        setMessageText(messageTextBlock.current.innerText);
        MessageStore.updateMessage(id, messageTextBlock.current.innerText);
    }, [])
    const textFilter = useCallback((event: KeyboardEvent<HTMLDivElement>) => {
        if (event.key === 'Enter') {
            setIsEditing(false);
            updateMessage();
        } else if (event.key === 'Escape') {
            setIsEditing(false);
            setMessageText(data);
        }
    }, [])

    useEffect(() => {
        if (isEditing) {
            messageTextBlock.current.focus();
        }
    }, [isEditing])

    return (
        <div style={{width: '100%', margin: '4px 0', position: 'relative', display: 'flex', ...author === +USERID ? {flexDirection: 'row-reverse', flexWrap: 'nowrap'} : {}}}>
            <div className={styles.message} onClick={toggleActive}>
                <div className={styles.author}>{/*TODO ссылка на пользователя*/}{Author}</div>
                {isEditing &&
                    <div style={{
                        width: '100vw',
                        height: '100vh',
                        position: 'fixed',
                        zIndex: '999',
                        backgroundColor: 'rgba(0, 0, 0, 0.3)',
                        cursor: "pointer",
                        left: 0,
                        top: 0,
                    }} onClick={() => {SetNotification('Если вы хотите принять изменения, нажмите "Enter", иначе нажмите "Escape".')}}/>
                }
                <div ref={messageTextBlock} style={isEditing ? {zIndex: '1000', border: 'none', outline: 'none'} : {}} contentEditable={isEditing} suppressContentEditableWarning={false} onKeyUp={textFilter}>{messageText}</div>
                <div className={styles.date}>{date}</div>
                {isActive && author === +USERID &&
                    <div className={styles.bottomLine}>
                        <EditIcon className={styles.button} onClick={() => {setIsEditing(!isEditing)}}/>
                        <DeleteIcon onClick={deleteMessage} className={styles.button}/>
                    </div>
                }
            </div>
        </div>
    );
});

