export const DOMAIN = 'http://localhost:4040';

export const performJSON = async <dataT, responseT>(url:string, data:dataT, config?: any): Promise<responseT> => {
    return fetch (`${DOMAIN}/${url}`, {
        method: 'POST',
        ...config,
        body: JSON.stringify(data),
        credentials: 'include',
        headers: {
            'Content-type': 'application-json'
        }
    }).then (
        response => {
            return response.json();
        }
    )
}
export const performString = async <dataT, responseT>(url:string, data:dataT, config?: any): Promise<responseT> => {
    return fetch (`${DOMAIN}/${url}`, {
        method: 'POST',
        ...config,
        body: data,
    }).then (
        response => {
            return response.json ();
        }
    )
}