import React, {FC, memo, useCallback, useEffect, useRef, useState} from 'react';
import styles from '../../styles/ChatsBlock.module.scss';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import {Chat, ChatsStore} from "../../store/ChatsStore";
import {Chats} from "./Chats";

interface Props {
    name: string;
    chats: number[];
    isStandard: boolean;
}

export const ChatList:FC<Props> = memo(({chats, name, isStandard}) => {

    const [isOpen, setIsOpen] = useState(false);
    const [isChanging, setIsChanging] = useState(false);
    const nameTextRef = useRef<HTMLDivElement>(null);

    const endChanging = useCallback(() => {
        ChatsStore.renameChatList((nameTextRef.current as HTMLDivElement).innerText, name)
        setIsChanging(false);
    }, []);

    useEffect(() => {
        if (isChanging) (nameTextRef.current as HTMLDivElement).focus();
    }, [isChanging]);

    return (
        <div className={styles.ChatList}>
            <div className={styles.name} onClick={isOpen ? () => {setIsOpen(false)} : () => {setIsOpen(true)}}>
                <div ref={nameTextRef} contentEditable={isChanging ? 'true' : 'false'} suppressContentEditableWarning={true} style={!isStandard ? {width: '193px', marginRight: '3px'} : {}} className={isChanging ? styles.text + ' ' + styles.active : styles.text}>{
                    name === 'favourites'
                        ? 'Важные'
                        :
                    name === 'friends'
                        ? 'Друзья'
                        :
                    name === 'subscribers'
                        ? 'Подписчики'
                        :
                    name === 'nobody'
                        ? 'Остальные чаты': name
                }</div>
                {
                    !isStandard
                        ?
                            <EditIcon onClick={() => {if (isChanging) {setIsChanging(false);endChanging();} else {setIsChanging(true)}}} style={{fill: 'rgba(0, 0, 0, .1)', height: '32px'}}/>
                        :
                            <></>
                }
                {isChanging
                    ?
                        <>
                            <div onClick={endChanging} style={{zIndex: '999', position: 'fixed', width: '100vw', height: '100vh', top: '0', left: '0'}}/>
                            <DeleteIcon style={{fill: 'rgba(0, 0, 0, .1)', height: '32px', marginLeft: '5px', zIndex: '1000'}} onClick={() => {endChanging();ChatsStore.deleteChatList(name)}}/>
                        </>
                    :
                        <svg viewBox="0 0 24 24" style={{fill: 'rgba(0, 0, 0, .1)', cursor: "pointer", width: '32px',marginRight: '5px',transform: isOpen ? 'rotate(0)' : 'rotate(180deg)'}}>
                            <path d="m4 12 1.41 1.41L11 7.83V20h2V7.83l5.58 5.59L20 12l-8-8-8 8z"></path>
                        </svg>
                }
            </div>
            {isOpen
                ? <Chats name={name} chats={chats} isDefault={isStandard}/>
                : <></>
            }
        </div>
    );
});