import React, {memo} from "react";
import InfiniteInput from "../UI/InfiniteInput";
import classes from "./../../../styles/HeaderAndMusicPanelStyles.module.css";
import {FC} from "react";

const MusicSearchLine:FC<{FSearchMusic: any}> = memo((probs) => {

    return (
        <div className={classes.MusicSearchLine}>
            <InfiniteInput
                 Pplaceholder="Введите название или автора песни "
                 FSearchMusic = {probs.FSearchMusic}
                 id = "MusicSearchLineInput"
            />
        </div>
    );

})

export default MusicSearchLine;