import React, {FC} from "react";

export const SpeedButton:FC<{number: number, Function: any}> = ({number, Function}) => {

    const onClick = () => {
        (document.getElementById('Video') as HTMLVideoElement).playbackRate = number;
        Function();
    };

    return (
        <div style={{color: 'white', padding: '6px', backgroundColor: 'grey', width: '40px', textAlign: 'center', cursor: 'pointer',
            border: 'white 1px solid', display: 'inline-block', fontSize:'20px'}} onClick={onClick}>
            {number}
        </div>
    )
}