import React, {FC, useCallback, useMemo, useRef, useState} from 'react';
import {observer} from "mobx-react-lite";
import {LazyImg} from "../../helper/LazyImg";
import {User, UsersStore} from "../../store/UsersStore";
import {Chat, ChatsStore} from "../../store/ChatsStore";
import {AlreadyInput} from "../../helper/AlreadyInput";
import {UsersToEditMembersBlock} from "./UI/UsersToEditMembersBlock";
import {GetAvatarSrc} from "../../functions/GetAvatarSrc";
import DeleteIcon from '@mui/icons-material/Delete';
import styles from "../../styles/MessageBLock.module.scss";
import {UploadFile} from "../../helper/UploadFile";

interface Props {
    Chat: Chat;
    setIsEditingWindowOpen: (value: boolean) => void;
}

export const EditMenu:FC<Props> = observer(({Chat, setIsEditingWindowOpen}) => {
    const nameRef = useRef<HTMLInputElement>(null);
    const avatarUploadRef = useRef<HTMLInputElement>(null);
    const [isDeletingWindowOpen, setIsDeletingWindowOpen] = useState(false);
    const [isDeleteIconOnHover, setIsDeleteIconOnHover] = useState(false);
    const [realMembers, setRealMembers] = useState<number[]>(Chat.members.map(Number));
    const [isUploadingAnAvatar, setIsUploadingAnAvatar] = useState(false);
    //Here we save new members, but not delete old members (on start).
    const [members, setMembers] = useState<number[]>(realMembers);
    const avatarSrc: string = useMemo(() => GetAvatarSrc(Chat), [ChatsStore.chatsList, Chat]);
    const [foundUsers, setFoundUsers] = useState<User[]>([]);
    const [isAddingWindowOpen, setIsAddingWindowOpen] = useState(false);
    let callbackToAdd: any;
    const findUserToAdd = useCallback((e: any) => {
        const value = e.target.value, array:User[] = [];
        if (value === '') {
            setFoundUsers([]);
            return;
        }
        clearTimeout(callbackToAdd);
        callbackToAdd = setTimeout(() => {
            for (let i = 0; i < UsersStore.users.length; i++) {
                if ((UsersStore.users[i].Name as string).toLowerCase().indexOf(value) != -1 || (UsersStore.users[i].Surname as string).toLowerCase().indexOf(value) != -1)
                    array.push(UsersStore.users[i])
            }
            setFoundUsers([...array]);
        }, 1500)
    }, []);
    const findUserInMembers = useCallback((e: any) => {
        const value = e.target.value, array:User[] = [];
        if (value === '') {
            setFoundUsers([]);
            return;
        }
        clearTimeout(callbackToAdd);
        callbackToAdd = setTimeout(() => {
            const newList = UsersStore.users.filter(user => members.indexOf(user.ID) > -1);
            for (let i = 0; i < members.length; i++) {
                if ((newList[i].Name as string).toLowerCase().indexOf(value) != -1 ||(newList[i].Surname as string).toLowerCase().indexOf(value) != -1)
                    array.push(UsersStore.users[i])
            }
            setFoundUsers([...array]);
        }, 1500)
    }, []);
    const setIsMember = useCallback((value: number) => {
        const index = realMembers.indexOf(value);
        if (index != -1) {
            const newMembers = [...realMembers];
            newMembers.splice(index, 1);
            setRealMembers(newMembers);
        } else {
            setRealMembers([...realMembers, value]);
            setMembers([...members, value]);
        }
    }, [realMembers, members]);

    const uploadOnClick = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
        UploadFile(e.target.files[0])
            .then((res) => {
                if (res != null && res.type == "Image") {
                    ChatsStore.updateChat(Chat.id, Chat.name, Chat.members, res.src);
                }
            });
    }, [avatarUploadRef]);
    const uploadOnDrop = useCallback((e: any) => {
        e.preventDefault();
        e.stopPropagation();
        UploadFile(e.dataTransfer.files[0])
            .then((res) => {
                if (res != null && res.type == "Image") {
                    ChatsStore.updateChat(Chat.id, Chat.name, Chat.members, res.src);
                }
            });
    }, []);

    const acceptChange = useCallback(() => {
        ChatsStore.updateChat(Chat.id, (nameRef.current as HTMLInputElement).value, realMembers.map(Number), Chat.avatar);
    },[realMembers, Chat]);

    return (
        <>
            <div style={{padding: '5px', backgroundColor: 'rgba(0, 0, 0, .1)', display: 'flex', flexFlow: 'column', borderRadius: '8px',
                    height: '150px', position: 'relative', left: '5px', top: '5px', justifyContent: "center", alignItems: 'center'}}>
                <LazyImg src={avatarSrc} ImageStyles={{width: '100px', height: '100px', padding: '5px', borderRadius: '50%'}}/>
                {isUploadingAnAvatar && <>
                    <input type="file" ref={avatarUploadRef} onChange={uploadOnClick} style={{display: 'none'}}/>
                    <div style={{width: '100vw', height: '100vh', position: 'fixed', top: '0', left: '0', zIndex: '999',
                        backgroundColor: 'rgba(0, 0, 0, .1)', cursor: 'pointer'}} onClick={() => {setIsUploadingAnAvatar(false)}}/>
                    <div onClick={() => {(avatarUploadRef.current as HTMLInputElement).click()}} onDrop={uploadOnDrop}
                         onDragStart={e => e.preventDefault()}
                         onDragOver={e => e.preventDefault()}
                         onDragLeave={e => e.preventDefault()}
                        style={{
                        cursor: 'pointer',
                        borderRadius: '8px',
                        border: '1px dashed rgba(0, 0, 0,.3)',
                        position: 'fixed',
                        left: '50%',
                        top: '50%',
                        transform: 'translate(-50%, -50%)',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        width: '400px',
                        height: '400px',
                        backgroundColor: 'white',
                        zIndex: '1000'
                    }}>
                        Переместите файл сюда или нажмите сюда
                    </div>
                </>}
                <div onClick={() => {setIsUploadingAnAvatar(true)}} style={{padding: '5px', backgroundColor: 'rgba(0, 0, 0, .1)', fontSize: '18px', color: 'white', borderRadius: '4px', cursor: 'pointer'}}>
                    Изменить аватар
                </div>
            </div>
            <div style={{width: '367px'}}>
                <div style={{padding: '5px', borderRadius: '8px', backgroundColor: 'rgba(0, 0, 0, .1)', margin: '5px'}}>
                    <AlreadyInput design={'default'} length={31} reg={'all'} ref={nameRef} value={Chat.name} Style={{margin: '0'}} width={'328px'} placeholder={'Название чата'}/>
                </div>
                <div style={{padding: '5px', borderRadius: '8px', backgroundColor: 'rgba(0, 0, 0, .1)', margin: '5px', fontSize: '16px',
                    overflow: 'hidden', overflowY: 'scroll', textAlign: 'center', maxHeight: '400px'}}>
                    Количество участников: {realMembers.length}
                    <div style={{display: 'flex', flexFlow: 'column', justifyContent: 'center', alignItems: 'center'}}>
                        <div onClick={() => {setIsAddingWindowOpen(!isAddingWindowOpen);setFoundUsers([]);clearTimeout(callbackToAdd)}}
                            style={{borderBottom: 'solid 1px lightgrey', padding: '3px 0', borderRadius: '4px', backgroundColor: 'rgba(0, 0, 0, .1)', width: '100%', cursor: 'pointer'}}>
                            {isAddingWindowOpen ? 'Перейти к списку участников' : 'Добавить участника'}
                        </div>
                        {isAddingWindowOpen
                            ?    <div><AlreadyInput placeholder={'Найти пользователя'} design={'default'} onChange={findUserToAdd} onEnter={findUserToAdd}
                                                    width={'326px'} Style={{height: '32px', top: '4px', position: 'relative'}} length={120} reg={'all'}/></div>
                            :   <><AlreadyInput placeholder={'Найти участника'} design={'default'} onChange={findUserToAdd} onEnter={findUserInMembers}
                                                   width={'326px'} Style={{height: '32px', top: '4px', position: 'relative'}} length={120} reg={'all'}/></>
                        }
                        <UsersToEditMembersBlock isMembersBlock={false} setIsMember={setIsMember} title={'Найденные поиском'} users={foundUsers} members={realMembers}/>
                        <UsersToEditMembersBlock isMembersBlock={!isAddingWindowOpen} title={isAddingWindowOpen ? 'Пользователи' : 'Участники'} setIsMember={setIsMember}
												 users={isAddingWindowOpen ? UsersStore.users : UsersStore.users.filter(user => members.indexOf(user.ID) != -1)} members={realMembers}/>
                    </div>
                </div>
                <div style={{display: 'flex', position: 'absolute', right: '5px',  flexFlow: 'column'}}>
                    <div style={{padding: '5px', backgroundColor: 'rgb(0, 199, 123)', cursor: 'pointer', fontSize: '20px', textAlign: 'center', width: '210px',
                        borderRadius: '4px', margin: '5px'}} onClick={() => {setIsEditingWindowOpen(false);acceptChange()}}>Принять изменения</div>
                    <div style={{display: 'flex', width: '220px', marginLeft: '5px', justifyContent: 'space-between', alignItems: 'center'}}>
                        <div style={{padding: '5px', backgroundColor: 'rgb(255, 51, 51)', cursor: 'pointer', fontSize: '18px', borderRadius: '4px'}}
                            onClick={() => {setIsEditingWindowOpen(false)}}>Отклонить изменения</div>
                        <div style={{position: 'relative'}}>
                            <DeleteIcon style={{color: 'rgb(255, 51, 51)', padding: '5px', cursor: 'pointer'}} onMouseEnter={() => {setIsDeleteIconOnHover(true)}}
                                        onClick={() => {setIsDeletingWindowOpen(true)}} onMouseLeave={() => {setIsDeleteIconOnHover(false)}} className={isDeleteIconOnHover ? styles.hover : ''}
                            />
                            {isDeletingWindowOpen
                                ?
                                <>
                                    <div onClick={() => setIsDeletingWindowOpen(false)}
                                         style={{position: 'fixed', left: '0', top: '0', width: '100vw', height: '100vh', backgroundColor: 'rgba(0, 0, 0, .1)', zIndex: '999'}}/>
                                    <div style={{textAlign: 'center', zIndex: '1000', position: 'absolute', right: '0px', top: '50px', width: '500px', borderRadius: '10px', height: '85px',
                                        backgroundColor: 'lightgrey'}}
                                    >
                                        <div>Вы уверены, что хотите удалить чат {Chat.name}? Это действие невозможно отменить.</div>
                                        <div style={{display: 'flex', position: 'relative', left: '200px', width: '310px'}}>
                                            <div style={{padding: '5px', cursor: 'pointer', backgroundColor: '#00c77b', borderRadius: '6px', marginRight: '5px'}} onClick={() => setIsDeletingWindowOpen(false)}>Отменить действие</div>
                                            <div style={{marginRight: '10px', padding: '5px', cursor: 'pointer', backgroundColor: '#ff3333', borderRadius: '6px'}}
                                                 onClick={() => {setIsDeletingWindowOpen(false);ChatsStore.deleteChat(ChatsStore.whoIsActive);}}
                                            >
                                                Удалить чат
                                            </div>
                                        </div>
                                    </div>
                                </>
                                : <></>
                            }
                        </div>
                    </div>
                </div>
            </div>

        </>
    );
});