import React, {memo, FC} from 'react';
import styles from '../../Styles/style.module.scss';
import {VideoStore} from "../../../../store/stores/VideoStore";
import {observer} from "mobx-react-lite";

export const TopController: FC = observer(() => {

    return (
        <div className={styles.TopController} id={'Video_TopController'}>
            <div className={styles.name}>
                {VideoStore.name}
            </div>
        </div>
    );

});