import {action, observable, runInAction} from "mobx";
import {DOMAIN} from "../helper/api";
import {Connection} from "../Connection";
import {binarySearchObject, binarySearchObjectSync, binarySortObject} from "../helper/binaryOperation";
import {USERID} from "../index";
import {hand, RestApiMethods} from "../helper/hand";
import {ConvertToPQArray} from "../helper/ConvertToPQArray";
import {ChatsStore} from "./ChatsStore";

export interface MessageDTO {
    author: number;
    date: string;
    data: string;
    files: string[];//{type: 'Image' | 'Video' | 'Audio' | 'Other', src: string}[];
}

export interface Message {
    id: number;
    author: number;
    chatKey: number;
    date: string;
    data: string;
    files: string[];
}

interface MessageFromServer {
    id: number;
    parent_chat_id: number;
    parent_user_id: number;
    data: string;
    date: string;
    files: string[];
    message_parent_id: 0 | number;
}

function convertMessageFromServer(message: MessageFromServer): Message {
    return {
        data: message.data,
        chatKey: message.parent_chat_id,
        id: message.id,
        author: message.parent_user_id,
        date: message.date,
        files: message.files
    }
}
//
// type MessageDTO struct {
//     ID              int64    `json:"id"`
//     ParentChatID    uint     `json:"parent_chat_id" binding:"required"`
//     ParentUserID    uint     `json:"parent_user_id" binding:"required"`
//     Data            string   `json:"data" binding:"required"`
//     Files           []string `json:"files" binding:"required"`
//     MessageParentID uint     `json:"message_parent_id"`
// }

interface MessageStoreInterface {
    /**
     * @Status: binary sorted
     * */
    chats: {
        chatkey: number;
        messages: Message[];
        getUntil: number;
    }[],
    isFetching: boolean;
    offlineMessageFrom: number[] | null;

    // sendFirstMessage: (message: MessageDTO, members: number[], name: string | "", avatar: string | "") => void;
    sendMessage: (message: MessageDTO, chatKey: number, messageParentId: number | 0) => void;
    deleteMessage: (id: number) => void;
    updateMessage: (id: number, newData: string) => void;
    getMessages: (chatKey: number) => Promise<any>;
    getLastMessages: (chatKeys: string[]) => Promise<any>;

    reactToMessage: (data: Message) => void;
    /**
     * @Param data has new text data and message id!
     * */
    reactToUpdateMessage: (data: [string, number]) => void;
    reactToDeleteMessage: (messageId: number) => void;
}

interface rawMessagesManagerInterface {
    //List to save raw messages. Raw messages are messages that we can't add to chat, because chat isn't ready.
    stack: {message: Message, chatKey: number}[],
    isActive: boolean,

    addRawMessage: (data: {message: Message, chatKey: number}) => void;
    callback: () => void;
}

const rawMessagesManager: rawMessagesManagerInterface = {
    stack: [],
    isActive: false,

    addRawMessage: (data: {message: Message, chatKey: number}) => {
        rawMessagesManager.stack.push(data);
        if (!rawMessagesManager.isActive) {
            rawMessagesManager.callback();
            rawMessagesManager.isActive = true;
        }
    },

    callback: () => {
        let timer = setInterval(() => {
            for (let i = rawMessagesManager.stack.length - 1; i >= 0; i--) {
                const {index, found} = binarySearchObjectSync(MessageStore.chats, rawMessagesManager.stack[0].chatKey, 'chatkey', true) as {index: number, found: boolean}
                if (found) {
                    runInAction( () => {
                        MessageStore.chats[index].messages.push(rawMessagesManager.stack[0].message);
                    })
                    rawMessagesManager.stack.shift();
                } else {
                    const newChat = {
                        chatkey: rawMessagesManager.stack[0].chatKey,
                        messages: [rawMessagesManager.stack[0].message],
                        getUntil: 1,
                    }
                    runInAction(() => {
                        MessageStore.chats.insertObj(newChat, 'chatkey')
                    })
                    rawMessagesManager.stack.shift();
                }
            }
            if (rawMessagesManager.stack.length == 0) {
                clearInterval(timer);
                rawMessagesManager.isActive = false;
            }
        }, 100);
    }
}

export const MessageStore = observable<MessageStoreInterface>({
    chats: [], isFetching: false, offlineMessageFrom: null,

    sendMessage: action(( message, chatKey, messageParentId) => {
        Connection.sendMessage({MessageParentID: messageParentId, Data: message.data, Files: message.files, ParentChatID: chatKey, ParentUserID: +USERID})
    }),

    getLastMessages: action((chatKeys) => {
        if (chatKeys.length === 0) return new Promise((res) => {res(undefined)});
        MessageStore.isFetching = true;
        const param = ConvertToPQArray(chatKeys);
        return hand(`/api/message/last?chats_id=${param}`, RestApiMethods.Get)
            .then(async res => {
                if (res.status === 200) {
                    return (await res.json()).messages;
                }
            })
            .then ((Response: MessageFromServer[]) => {
                const Messages = [];
                for (let i = Response.length - 1; i >= 0; i--) {
                    Messages.push(convertMessageFromServer(Response[i]));
                }
                const array: any[] = [...MessageStore.chats];
                for (let i = 0; i < Messages.length; i++) {
                    array.splice(-(binarySearchObjectSync(array, Messages[i].chatKey, 'chatkey') as number + 1), 0,
                        {
                            chatkey: Messages[i].chatKey,
                            messages: [Messages[i]],
                            getUntil: 1
                        }
                    );
                }
                runInAction(() => {MessageStore.chats = array})
            })
    }),

    getMessages: action((chatKey:number) => {
        if (chatKey === undefined || chatKey === 0) return new Promise((res) => {res(undefined)});
        const existsMessages = MessageStore.chats.find(chat => chat.chatkey === chatKey);
        if (!existsMessages) throw new Error('Chat not found');
        return hand(`/api/message/${chatKey}?offset=${existsMessages.getUntil}`, RestApiMethods.Get)
            .then(async res => {
                if (res.status === 200) {
                    return (await res.json()).messages;
                }
            })
            .then(async (Res: MessageFromServer[]) => {
               const messages = [];
               for (let i = Res.length - 1; i >= 0; i--) {
                   const message = convertMessageFromServer(Res[i]);
                   if (message.id > 0) {
                       messages.push(message);
                   }
               }
                const index = await binarySearchObject(MessageStore.chats, chatKey, 'chatkey') as number,
                    newMessageStoreChats = JSON.parse(JSON.stringify(MessageStore.chats)), Messages = messages.filter((value: Message) => value.id > 0),
                    array = newMessageStoreChats[index].messages.concat(Messages);
                newMessageStoreChats[index].getUntil += 20;
                //First two elements can be repeated. We need to check it.
                newMessageStoreChats[index].messages = binarySortObject(array, 'id');
                runInAction(() => {
                    if (Messages.length < 20) {
                        const ChatSearch = binarySearchObjectSync(ChatsStore.chats, chatKey, 'id', false) as number;
                        ChatsStore.chats[ChatSearch].isGet = true;
                    }
                    MessageStore.chats = newMessageStoreChats;
                })
                return;
            })
            .finally(() => {return;})
    }),

    deleteMessage: action((id) => {
        Connection.deleteMessage(id)
    }),

    updateMessage: action((id, newData) => {
        Connection.updateMessage(id, newData)
    }),

    reactToMessage: action(async (data) => {
        const message: Message = {
            id: data.id,
            data: data.data,
            author: data.parent_user_id,
            date: data.date,
            files: data.files,
            chatKey: data.parent_chat_id,
        }
        rawMessagesManager.addRawMessage({message: message, chatKey: message.chatKey})
    }),

    reactToDeleteMessage: action(async (data) => {
        let isReady = false;
        for (let i = MessageStore.chats.length - 1; i >=0; i--) {
            if (isReady) break;
            for (let j = MessageStore.chats[i].messages.length - 1; j >= 0; j--) {
                if (MessageStore.chats[i].messages[j].id === data) {
                    isReady = true;
                    runInAction(() => {
                        MessageStore.chats[i].messages.splice(j, 1);
                    })
                    break;
                }
            }
        }
        // MessageStore.chats[await binarySearchObject(MessageStore.chats, data, 'chatkey') as number].messages.splice((
        //     await binarySearchObject(MessageStore.chats[await binarySearchObject(MessageStore.chats, data, 'chatkey') as number].messages, data, 'id') as number
        // ), 1)
    }),

    reactToUpdateMessage: action(async (data) => {
        let isReady = false;
        for (let i = MessageStore.chats.length - 1; i >=0; i--) {
            if (isReady) break;
            for (let j = MessageStore.chats[i].messages.length - 1; j >= 0; j--) {
                if (MessageStore.chats[i].messages[j].id === data[1]) {
                    isReady = true;
                    runInAction(() => {
                        MessageStore.chats[i].messages[j].data = data[0];
                    })
                    break;
                }
            }
        }
        // MessageStore.chats[await binarySearchObject(MessageStore.chats, chatKey, 'chatkey') as number].
        //     messages[(await binarySearchObject(MessageStore.chats[await binarySearchObject(MessageStore.chats, chatKey, 'chatkey') as number]
        //     .messages, messageId, 'id')) as number].data = newMessage;
    })
})