import {action, observable, runInAction} from "mobx";
import {DOMAIN} from "../helper/api";
import {User, UsersStore} from "./UsersStore";
import {
    binaryInsert,
    binaryInsertObjectSync,
    binaryInsertSync,
    binarySearch,
    binarySearchObject, binarySearchSync,
    binarySortObject,
} from "../helper/binaryOperation";
import {Connection} from "../Connection";
import {SetNotification} from "../helper/SetNotification";
import {USERID} from "../index";
import {hand, RestApiMethods} from "../helper/hand";
import {ChatList} from "../Components/ChatsBlock/ChatList";

export interface ChatDTO {
    /**
     * only conversations has it
     * */
    name: string | null;
    members: number[];
    id: number;
    /**
     * @Format '/UserFiles/' + members[0] + '/Image/' + avatar
     * */
    avatar: null | string;
}

export interface Chat extends ChatDTO{
    isGet?: boolean;
    isConversation: boolean;
}

export interface ChatList {
    name: string;
    chats: number[]
}

interface ChatsStoreInterface {
    chatsList: ChatList[];
    isReady: boolean;
    /**@Status binary*/
    chats: Chat[];
    /**
     * ID of the chat with whom the client chatting now.
     * */
    whoIsActive: number;

    getReady: () => Promise<void | Error>;
    createChat: (name: string | null, members: number[], avatar: string) => any;
    checkChatLists: () => any;
    deleteChatList: (name: string) => void;
    updateChat: (id: number, name: string | "", members: number[], avatar: string | "") => void;
    deleteChat: (id: number) => void;
    createNewChatList: (name: string, chats: number[]) => void;
    renameChatList: (newName: string, oldName: string) => boolean;
    ChangeChatsInChatList: (name: string, newChats: number[]) => void;

    reactToCreateChat: (chat: ChatDTO) => void;
    reactToUpdateChat: (chat: { chat_id: number, members: string, name:string, avatar: string | null }) => void;
    reactToDeleteChat: (idOfChat: number) => void;
}

export const ChatsStore = observable<ChatsStoreInterface>({
    chatsList: [],
    whoIsActive: -1,
    isReady: false,
    chats: [],

    getReady: action(() => {
        return hand('/api/chat/', RestApiMethods.Get)
            .then (async response => {
                    if (response.status === 200) {
                        return response.json();
                    }
            }).then (async (Response: {chatLists: string, chats: Chat[], avatar: string, name: string, surname: string, friends: number[], subscribers: number[]}) => {
                if (Response.chatLists == undefined) {
                    SetNotification("Неожиданная ошибка!")
                    return Error("500;No chat lists found");
                }
                if (typeof Response.avatar != 'string') Response.avatar = ''
                Response.chats = binarySortObject(Response.chats, 'id');
                UsersStore.FindYourselves(Response.friends, Response.subscribers, Response.avatar, Response.name, Response.surname);
                runInAction(() => {
                    ChatsStore.chats = Response.chats;
                })

                const uniqueIdOfUsersArray: number[] = [];
                for (let i = 0;i < Response.chats.length;i++) {
                    for (let j = 0;j < Response.chats[i].members.length;j++) {
                        await binaryInsert(uniqueIdOfUsersArray, Response.chats[i].members[j]);
                    }
                }
                uniqueIdOfUsersArray.splice(binarySearchSync(uniqueIdOfUsersArray, +USERID, true).index, 1)
                await UsersStore.GetUsers(uniqueIdOfUsersArray.map(String));
                await addNewChatsToLists(Response.chats, JSON.parse(Response.chatLists));
                for (let i = 0; i < Response.chats.length;i++) {
                    Response.chats[i].isGet = false;
                    if ((Response.chats[i].name == null || Response.chats[i].name == '') && Response.chats[i].members.length < 3) {
                        const parse = Response.chats[i].members;
                        if (parse.length > 2) {
                            Response.chats[i]['isConversation'] = true;
                        } else Response.chats[i].isConversation = false;
                        if (parse[0] == +USERID) {
                            const find = UsersStore.users.find(User => User.ID == parse[1]);
                            Response.chats[i].name = find.Surname + ' ' + find.Name
                        } else {
                            const find = UsersStore.users.find(User => User.ID == parse[0]);
                            Response.chats[i].name = find.Surname + ' ' + find.Name
                        }
                    } else {
                        Response.chats[i]['isConversation'] = true;
                    }
                }
                Connection.start(uniqueIdOfUsersArray.map(String))
                runInAction(() => {
                    ChatsStore.checkChatLists();
                    ChatsStore.isReady = true;
                    ChatsStore.chats = Response.chats;
                })
            })
    }),

    checkChatLists: action(async () => {
        let isChanged:boolean = false;
        ChatsStore.chats = binarySortObject(ChatsStore.chats, 'id');
        for (let i = 0;i < ChatsStore.chatsList.length;i++) {
            for (let j = 0;j < ChatsStore.chatsList[i].chats.length;j++) {
                // @ts-ignore
                await binarySearchObject(ChatsStore.chats, ChatsStore.chatsList[i].chats[j], 'id', true).then(res => {// @ts-ignore
                    if (res.found === false) {
                        ChatsStore.chatsList[i].chats.splice(j, 1);
                        isChanged = true;
                    }
                })
            }
        }
        const friends: number[] = ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats,
        nobody: number[] = ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats,
        subs: number[] = ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats;
        const lengthF = +friends.length;
        const lengthS = +subs.length;
        const lengthN = +nobody.length;//TODO There filters only half array. (Maybe fixed)
        for (let i = 0;i < lengthF;i++) {
            const value = ChatsStore.chats.find(Chat => Chat.id === friends[i]);
            if (value == undefined) {
                isChanged = true;
                // @ts-ignore
                ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.splice(i, 1);
            } else {
                if (value.members.length == 2) {
                    if (value.members[0] == +USERID) {
                        if ((UsersStore.Yourselves?.Friends).indexOf(value.members[1]) == -1) {
                            isChanged = true;
                            if ((UsersStore.Yourselves?.Subscribers).indexOf(value.members[1]) != -1) { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.push(value.id);
                            }
                            else { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.push(value.id);
                            }
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.indexOf(value.id), 1)
                        }
                    } else {
                        if ((UsersStore.Yourselves?.Friends).indexOf(value.members[0]) == -1) {
                            isChanged = true;
                            if ((UsersStore.Yourselves?.Subscribers).indexOf(value.members[0]) != -1) { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.push(value.id);
                            }
                            else { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.push(value.id);
                            }
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.indexOf(value.id), 1)
                        }
                    }
                }
            }
        }
        for (let i = 0;i < lengthS;i++) {
            const value = ChatsStore.chats.find(Chat => Chat.id === subs[i]);
            if (value == undefined) {
                isChanged = true;
                // @ts-ignore
                ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.splice(i, 1);
            } else {
                if (value.members.length == 2) {
                    if (value.members[0] == +USERID) {
                        if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[1]) == -1) {
                            isChanged = true;
                            if (UsersStore.Yourselves?.Friends.indexOf(value.members[1]) != -1) { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.push(value.id);
                            } else { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.push(value.id);
                            }
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.indexOf(value.id), 1)
                        }
                    } else {
                        if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[0]) == -1) {
                            isChanged = true;
                            if (UsersStore.Yourselves?.Friends.indexOf(value.members[0]) != -1) { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.push(value.id);
                            } else { // @ts-ignore
                                ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.push(value.id);
                            }
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.indexOf(value.id), 1)
                        }
                    }
                }
            }
        }
        for (let i = 0;i < lengthN;i++) {
            const value = ChatsStore.chats.find(Chat => Chat.id === nobody[i]);
            if (value == undefined) {
                isChanged = true;
                nobody.splice(i, 1);
            } else {
                if (value.members.length == 2) {
                    if (value.members[0] == +USERID) {
                        if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[1]) != -1) {
                            isChanged = true;
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.indexOf(value.id, 1));
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.push(value.id);
                        } else if (UsersStore.Yourselves?.Friends.indexOf(value.members[1]) != -1) {
                            isChanged = true;
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.indexOf(value.id, 1));
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.push(value.id);
                        }
                    } else {
                        if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[0]) != -1) {
                            isChanged = true;
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.indexOf(value.id, 1));
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'subscribers').chats.push(value.id);
                        } else if (UsersStore.Yourselves?.Friends.indexOf(value.members[0]) != -1) {
                            isChanged = true;
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.splice(ChatsStore.chatsList.find(ChatList => ChatList.name == 'nobody').chats.indexOf(value.id, 1));
                            // @ts-ignore
                            ChatsStore.chatsList.find(ChatList => ChatList.name == 'friends').chats.push(value.id);
                        }
                    }
                }
            }
        }

        if (isChanged) {
            updateChatListsFetch();
        }
    }),

    createChat: action(async (name, members, avatar) => {
        const usersToGet = [];
        members.forEach(id => {
            const index = UsersStore.users.searchObj(id, 'ID');
            if (index < 0) {
                usersToGet.push(id)
            }
        })
        if (usersToGet.length > 0) {
           await UsersStore.GetUsers(usersToGet.map(String));
        }
        Connection.createChat(name, avatar, members);
    }),

    createNewChatList: action((name, chats) => {
        if (ChatsStore.chatsList.find(value => value.name === name) != undefined) {SetNotification("Список чатов с названием " + name + " уже существует. Невозможно создать этот список!");return;}
        ChatsStore.chatsList.splice(3, 0, {name: name, chats: chats});
        ChatsStore.chatsList = [...ChatsStore.chatsList]
        updateChatListsFetch();
    }),

    renameChatList: action((newName, oldName) => {
        if (newName === oldName) return false;
        for (let i = 0; i < ChatsStore.chatsList.length; i++) {
            if (ChatsStore.chatsList[i].name === newName) {
                SetNotification("Список чатов с названием " + newName + " уже существует. Невозможно создать этот список!");
                return false;
            }
        }
        const newChatList: ChatList[] = JSON.parse(JSON.stringify(ChatsStore.chatsList));
        // @ts-ignore
        newChatList.find(ChatList => ChatList.name === oldName).name = newName;
        ChatsStore.chatsList = newChatList;
        updateChatListsFetch();
        return true;
    }),

    deleteChatList: action((name) => {
        const newChatList: ChatList[] = JSON.parse(JSON.stringify(ChatsStore.chatsList));
        for (let i = 0; i < newChatList.length; i++) {
            if (newChatList[i].name === name) {
                newChatList.splice(i, 1);
                ChatsStore.chatsList = newChatList;
                updateChatListsFetch();
                break;
            }
        }
    }),

    ChangeChatsInChatList: action((name, newChats) => {
        const newChatList = JSON.parse(JSON.stringify(ChatsStore.chatsList))
        // @ts-ignore
        newChatList.find(ChatList => ChatList.name === name).chats = newChats;
        runInAction(() => {
            ChatsStore.chatsList = newChatList;
        })
        updateChatListsFetch();
    }),

    updateChat: action((id, name, members, avatar) => {
        Connection.updateChat(name, avatar, members, id)
    }),

    deleteChat: action((id) => {
        Connection.deleteChat(id)
    }),

    reactToCreateChat: action((chat) => {
        const where = (() => {
            if (chat.members.length == 2) {
                if (chat.members[0] == +USERID) {
                    if (UsersStore.Yourselves?.Friends.indexOf(+chat.members[1]) != -1) return 'friends';
                    else if (UsersStore.Yourselves?.Subscribers.indexOf(+chat.members[1]) != -1) return 'subscribers';
                    else return 'nobody';
                } else {
                    if (UsersStore.Yourselves?.Friends.indexOf(+chat.members[0]) != -1) return 'friends';
                    else if (UsersStore.Yourselves?.Subscribers.indexOf(+chat.members[0]) != -1) return 'subscribers';
                    else return 'nobody';
                }
            }
            return 'nobody';
        })();
        if (chat.name.length < 1) {
            if (chat.members[0] == +USERID) {
                const user: User = UsersStore.users.getByKey(chat.members[1], 'ID');
                chat.name = user.Name + ' ' + user.Surname
            } else {
                const user: User = UsersStore.users.getByKey(chat.members[0], 'ID');
                chat.name = user.Name + ' ' + user.Surname
            }
        }
        const newChatList = JSON.parse(JSON.stringify(ChatsStore.chatsList)), newChats = JSON.parse(JSON.stringify(ChatsStore.chats));
        // @ts-ignore
        newChatList.find(ChatList => ChatList.name == where).chats.push(chat.id);
        binaryInsertObjectSync(newChats, chat, 'id');
        runInAction(() => {
            ChatsStore.chats = newChats;
            ChatsStore.chatsList = newChatList;
        })
        updateChatListsFetch();
    }),

    reactToUpdateChat: action(async (chat) => {
        const {index} = await binarySearchObject(ChatsStore.chats, chat.chat_id,'id',true) as {found: boolean, index: number};
        const correctChat = {
            name: chat.name,
            members: chat.members,
            avatar: chat.avatar,
            isGet: ChatsStore.chats[index].isGet,
            id: chat.chat_id,
            isConversation: chat.name != null && chat != '' || chat.members.length > 2
        }
        runInAction(() => {
            ChatsStore.chats[index] = correctChat;
        })
    }),

    reactToDeleteChat: action(async (idOfChat) => {
        const newChatsStore = JSON.parse(JSON.stringify(ChatsStore.chats)), newChatsLists = JSON.parse(JSON.stringify(ChatsStore.chatsList));
        newChatsStore.splice(await binarySearch(ChatsStore.chats, idOfChat), 1);

        for (let i = 0; i < ChatsStore.chatsList.length;i++) {
            const res = await binarySearch(ChatsStore.chatsList[i].chats, idOfChat, true) as {found: boolean;index: number};
            if (res.found) {
                newChatsLists[i].chats.splice(res.index, 1);
            }
        }
        console.log(ChatsStore.chats)
        runInAction(() => {
            ChatsStore.chatsList = newChatsLists;
            ChatsStore.whoIsActive = -1;
        })
        updateChatListsFetch();
    })
})

/** When user is offline they can not add new chat to list. So new chat adds to all chats and here we find all new chats and add it to correct list
 *  @param allChats is sorted
 *  @param chatLists
 * */
const addNewChatsToLists = async (allChats: Chat[], chatLists: ChatList[]) => {
    const friends: number[] = [], subs: number[] = [], nobody: number[] = [], acceptedChats: number[] = [];
    for (let i = 0; chatLists.length > i; i++) {
        for (let j = 0; chatLists[i].chats.length > j; j++) {
            binaryInsertSync(acceptedChats, chatLists[i].chats[j]);
        }
    }
    for (let i = 0; i < allChats.length; i++) {
        if (acceptedChats.indexOf(allChats[i].id) == -1) {
            const value = allChats[i];
            if (value.members.length == 2) {
                if (value.members[0] == +USERID) {
                    if (UsersStore.Yourselves?.Friends.indexOf(value.members[1]) != -1) friends.push(value.id);
                    else if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[1]) != -1) subs.push(value.id);
                    else nobody.push(value.id)
                } else {
                    if (UsersStore.Yourselves?.Friends.indexOf(value.members[0]) != -1) friends.push(value.id);
                    else if (UsersStore.Yourselves?.Subscribers.indexOf(value.members[0]) != -1) subs.push(value.id);
                    else nobody.push(value.id)
                }

                nobody.push(value.id)
            } else {
            }
        }
    }
    if (friends.length > 0 || subs.length > 0 || nobody.length > 0) {
        if (friends.length > 0) (chatLists.find(chatList => chatList.name === 'friends') as ChatList).chats.push(...friends);
        if (subs.length > 0) (chatLists.find(chatList => chatList.name === 'subscribers') as ChatList).chats.push(...subs);
        if (nobody.length > 0) (chatLists.find(chatList => chatList.name === 'nobody') as ChatList).chats.push(...nobody);
    }
    runInAction(() => {
        ChatsStore.chatsList = chatLists;
    })
}

const updateChatListsFetch = () => {
    const obj = {
        newChatLists: `${JSON.stringify(ChatsStore.chatsList)}`,
    }
    hand('/api/chat/chat-list/', RestApiMethods.Patch, JSON.stringify(obj))
        .then( res => {
            switch (res.status) {
                case 204: {
                    return;
                }
                case 401: {
                    return Error("401; Unauthorized");
                }
                case 400: {
                    return Error("400; Bad Request");
                }
                case 500: {
                    return Error("500; Internal server error");
                }
            }
        })
}