import {action, observable} from "mobx";

interface ImageStoreInterface {
    isOpen: boolean;
    src: string;
    name: string;

    readonly openNewImage: (name: string, src: string) => void;
    readonly close: () => void;
}

export const ImageStore:ImageStoreInterface = observable({
    isOpen: false,
    src: '',
    name: '',

    openNewImage: action( (name: string, src: string) => {
        if (ImageStore.src !== src) {
            ImageStore.name = name;
            ImageStore.src = src;
        }
        ImageStore.isOpen = true;
    }),

    close: action( () => {
        ImageStore.isOpen = false;
    })
});