import React, {memo, FC, PropsWithChildren} from 'react';
import LeftMenu from "./FC/LeftMenu";
import {Header} from "./FC/Header";
import {VideoWindow} from "./FC/Video";
import {ImageWindow} from "./FC/Image";

interface Probs {
    children: any
}

export const MainLayOut: FC<PropsWithChildren<Probs>> = memo(({children}) => {

    return (
        <div className={"Interface"}>
            <LeftMenu/>
            <Header/>
            <VideoWindow/>
            <ImageWindow/>
            {children}
        </div>
    );

});