import React, {memo, FC, useState, useCallback} from 'react';
import {ChatsBox} from "./ChatsBox";
import {Chat} from "../../store/ChatsStore";
import {CreatingOrSearchChatWindow} from "./CreatingOrSearchChatWindow";

export const ChatBlock: FC = memo(() => {

    const [foundArray, setFoundArray] = useState<Chat[]>([]);
    const [isSearching, setIsSearching] = useState(false);
    const [isChatCreating, setIsChatCreating] = useState(false);

    const SetFoundArray = useCallback((array: Chat[]) => {
        setFoundArray(array)
    }, []);
    const SetIsSearching = useCallback((bool: boolean) => {
        setIsSearching(bool)
    }, [])
    const SetIsChatCreating = useCallback((bool: boolean) => {
        setIsChatCreating(bool)
    }, [])

    return (
        <div className={'RightColumn'} style={{height: '100vh - 55px', overflowY: 'scroll', overflowX: 'hidden'}}>
            <CreatingOrSearchChatWindow setIsChatCreating={SetIsChatCreating} setIsSearching={SetIsSearching} setFoundArray={SetFoundArray}/>
            <ChatsBox isChatCreating={isChatCreating} setIsChatCreating={SetIsChatCreating} isSearching={isSearching} foundArray={foundArray}/>
        </div>
    );
});