import React, {memo, FC, useCallback, useRef} from 'react';
import { AlreadyInput } from '../../../helper/AlreadyInput';
import {MessageDTO, MessageStore} from "../../../store/MessageStore";
import {binarySearchObjectSync} from "../../../helper/binaryOperation";
import {ChatsStore} from "../../../store/ChatsStore";
import {observer} from "mobx-react-lite";
import {USERID} from "../../../index";

export const MessageInput: FC = observer(({}) => {
    
    const input = useRef<HTMLInputElement>(null);
    const send = useCallback(() => {
        const text = (input.current as HTMLInputElement).value;
        for (let i = text.length; i > 0; i--) {// Checking for empty message
            if (text[i] != '' && text[i] != ' ' && text[i] != ' ') {
                const message: MessageDTO = {
                        author: +USERID,
                        files: [],
                        date: '',
                        data: text
                    };
                MessageStore.sendMessage(message, ChatsStore.whoIsActive, 0)
                break;
            }
        }
    }, [ChatsStore.whoIsActive.toString(), ChatsStore.whoIsActive]);

    return (
        <div className={"messageInputPrev"}>
            <svg width="32px" height="32px" style={{marginLeft: "10px", marginRight: "5px"}} viewBox="0 0 59 63">
                <g stroke="none" fill="none">
                    <path d="M36.3,46.5 L12.1,22.4 C9.4,19.7 9.1,15.6 11.8,12.9 L11.8,12.9 C14.5,10.2 19.1,10.1 21.8,12.8 L53.6,44.4 C57.8,48.6 58.2,54.9 54.5,58.5 L54.5,58.5 C50.8,62.1 44.5,61.7 40.3,57.6 L4.7,22.1 C-0.1,17.4 -0.2,9.8 4.3,5.3 L5.3,4.3 C9.8,-0.2 17.4,-0.1 22.2,4.7 L46,28.4" stroke="#6B6C6E"></path>
                </g>
            </svg>
            <AlreadyInput ref={input} length={255} width={'465px'} onEnter={send} onClick={send} Style={{height: '40px', margin: '0', marginRight: '5px'}} design={'arrowUp'} reg={'all'}/>
        </div>
    );
});