import {action, observable, runInAction} from "mobx";
import {
    binaryInsertSync,
    binarySearchSync,
    binarySortObject
} from "../helper/binaryOperation";
import {ConvertToPQArray} from "../helper/ConvertToPQArray";
import {hand, RestApiMethods} from "../helper/hand";
import {USERID} from "../index";

export interface User {
    Avatar: string;
    Name: string;
    Surname: string;
    ID: number;
}

interface CoolUser {
    Friends: number[]
    Subscribers: number[]
    Avatar: string;
    Name: string;
    Surname: string;
}

interface UsersStoreInterface {
    Yourselves: CoolUser | null
    users: User[];
    onlineUsers: number[];

    reactOnlineUsers: (res: any) => void;
    reactToDisconnect: (userId: number) => void;
    reactToConnect: (userId: number) => void;
    FindYourselves: (friends: number[], subscribers: number[], avatar: string, name: string, surname: string) => void
    GetUsers: (users: string[]) => Promise<void> | void
}

export const UsersStore = observable<UsersStoreInterface>({
    Yourselves: null,
    users: [],
    onlineUsers: [],

    FindYourselves: action((friends, subscribers, avatar, name, surname) => {
        UsersStore.Yourselves = {
            Name: name,
            Avatar: avatar,
            Friends: friends,
            Subscribers: subscribers,
            Surname: surname
        }
    }),

    GetUsers: action((users) => {
        if (users.length > 0) {
            return hand(`/api/user/many?idOfUsers=${ConvertToPQArray(users)}`, RestApiMethods.Get)
                .then(res => {
                    if (res.status === 200) {
                        return res.json()
                    }
                })
                .then((Res: {users: User[]}) => {
                    runInAction(() => {
                        UsersStore.users = binarySortObject([{Avatar: UsersStore.Yourselves.Avatar, ID: +USERID, Surname: UsersStore.Yourselves.Surname, Name: UsersStore.Yourselves.Name}, ...Res.users, ...UsersStore.users], 'ID');
                        UsersStore.users.isSorted = true;
                    })
                })
        }
    }),

    reactOnlineUsers: action((res) => {
        UsersStore.onlineUsers = res;
    }),

    reactToDisconnect: action((userId) => {
        UsersStore.onlineUsers.remove(userId)//UsersStore.onlineUsers.splice(binarySearchSync(UsersStore.onlineUsers, userId), 1);
    }),

    reactToConnect: action((userId) => {
        UsersStore.onlineUsers.insert(userId);//binaryInsertSync(UsersStore.onlineUsers, userId);
    })
})