import React, {memo, FC} from 'react';
import classes from "../../styles/HeaderAndMusicPanelStyles.module.css";
import {TopPanel} from "./TopPanel";
import ExitButtonInHeader from "./AI/ExitButtonInHeader";
export const Header: FC = memo(() => {

    return (
        <div className={classes.header} id={"header"}>
            <div className={classes.HeaderData}>
                <a className={classes.div_button_hey} href="/index.html"
                   style={{textDecoration: "none", color: "black"}}>
                    <div datatype={"submit"} className={classes.button_hey}>hey</div>
                </a>
                <div className={classes.MusicInSiteTopPanel} style={{left: "420px"}} id={"MusicInSiteTopPanel"}>
                    <TopPanel/>
                </div>
                <ExitButtonInHeader/>
            </div>
        </div>
    );

});