export function SetNotification(text: string):void { //Для корректной работы стоит подключить стили SetHTML.css
    let Notification = document.createElement("div");
    Notification.setAttribute("class", "Notification");
    Notification.innerText = text;
    if (document.getElementById("NotificationBlock") !== null) {
        // @ts-ignore
        document.getElementById("NotificationBlock").append(Notification);
    } else {}
    setTimeout ( function () {
        Notification.remove();
    }, 10000);
}