import React, {memo, FC, useState, useRef, useEffect, MouseEventHandler} from 'react';
import {SetLoading} from "./SetLoading";

interface Props {
    src: string;
    ImageStyles?: React.CSSProperties;
    DivToImageStyles?: React.CSSProperties;
    ImageClassName?: any;
    DivToImageClassName?: any;
    element?: any;
    Onclick?: MouseEventHandler<HTMLDivElement> | undefined;
}

export const LazyImg: FC<Props> = memo (({src, DivToImageStyles, ImageStyles, DivToImageClassName, Onclick, ImageClassName, element}) => {
    const [IsLoading, SetIsLoading] = useState(true), ImageRef = useRef<HTMLImageElement | null>(null)
    ,
    styles = {
        LazyImg: {
            ...DivToImageStyles
        }
    };

    useEffect( () => {
        if (ImageRef.current != null) {
            SetIsLoading(false);
            // @ts-ignore
            ImageRef.current.style.display = 'block';
        }
        if (ImageRef.current) {
            ImageRef.current.onload = () => {
                SetIsLoading(false);
                // @ts-ignore
                ImageRef.current.style.display = 'block';
            }
        }
    }, [])

    return (
        <div style={styles.LazyImg as React.CSSProperties} onClick={Onclick} className={DivToImageClassName}>
            {IsLoading
                ?
                    element != undefined
                        ? element
                        : <SetLoading styles={{top: "85px", left: "85px"}}/>
                :
                   ''
            }
            <img style={{...ImageStyles, display: 'none'}} className={ImageClassName} src={src} ref={ImageRef} alt={''}/>
        </div>
    );

});