import MusicLineInSiteMusic from "./MusicLineInSiteMusic";
import MusicList from "./MusicList";
import React, {memo, useCallback, useEffect, useState} from "react";
import classes from "../../styles/HeaderAndMusicPanelStyles.module.css";
import {MusicStore} from "../store/stores/MusicStore";

export const TopPanel = memo(() => {

    const [IsMusicListShowing, SetShowMusicList]/* Состояние отвечающее за видимость MusicList */ = useState(0);

    function ShowMusicList()/*:void Показываем MusicList*/{
        SetShowMusicList(1);
    }

    function hideMusicList()  /*:void скрываем MusicList*/{
        SetShowMusicList(0);
    }

    const listenerEnd = useCallback(() => {
        try {
            if (MusicStore.playingInSite) {// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInSite${MusicStore.NumberOFSong - 1}`).style.display = "none";// @ts-ignore
                document.getElementById(`ButtonPlayOFSongInSite${MusicStore.NumberOFSong - 1}`).style.display = "block";
            } else {// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInPost${MusicStore.NumberOFSong + 1}`).style.display = "none";// @ts-ignore
                document.getElementById(`ButtonPlayOFSongInPost${MusicStore.NumberOFSong + 1}`).style.display = "block";
            }// @ts-ignore
            (document.getElementById("SongInSiteAudio") as HTMLAudioElement).src = MusicStore.src;
        } catch (e) {}
        MusicStore.end();
    }, [MusicStore.number]);

    const timeUpDate = useCallback (() => {
        try {
            const newCurrentTime = (document.getElementById("SongInSiteAudio") as HTMLAudioElement).currentTime / (document.getElementById("SongInSiteAudio") as HTMLAudioElement).duration;
            (document.getElementById('SongInSiteProgressBar') as HTMLDivElement).style.width=newCurrentTime * 100 + '%';
        } catch (e) {}
    }, []);

    return (
        <div>
            <audio id='SongInSiteAudio'
                onEnded={listenerEnd}
                onTimeUpdate={timeUpDate}
            />
            <div className={classes.MusicInSiteTopPanel} id={"MusicInSiteTopPanel1"}>
                <MusicLineInSiteMusic
                    ShowMusicList = {ShowMusicList}
                />
            </div>
            {IsMusicListShowing == 1
                ?
                    <MusicList
                        FunctionHide={hideMusicList}
                    />
                :
                    ""
            }
        </div>
    )
})