import {action, observable, runInAction} from "mobx";
import {DOMAIN} from "../../../helper/api";

interface MusicStoreInterface {
    songName: string;
    playingInSite: boolean;
    number/* Номер играющей песни (number or null) */: number;
    lastVolume/*Громкость, которую пользователь указал для последней песни (Изначально 50)*/: number;
    isRepeated/*Повторяем ли песню*/: boolean;
    isPlaying /*Играет ли песня сейчас */:boolean;
    array/*JSON формат массива с песнями*/: {count: number, IsThereMore: boolean, array: {dir: string, date: string, name: string}[]} | null;
    src: string | null;

    readonly start: (name: string, src: string, number: number, playingInSite: boolean, increment?: boolean) => void;
    readonly end: () => void;
    readonly play: (inSite: boolean) => void;
    readonly pause: (inSite: boolean, number: number) => void;
    readonly addSongs: (object: {count: number, IsThereMore: boolean, array: {dir: string, date: string, name: string}[]}) => void;

    readonly setVolume: (value: number) => void;
    readonly setRepeated: (value: boolean) => void;
}

export const MusicStore: MusicStoreInterface = observable({
    songName: '', playingInSite: true, number: -1, lastVolume: 0.50, isRepeated: false, isPlaying: false, array: null, src: '',

    start: action( (name: string, src: string, number: number, playingInSite: boolean, increment: boolean = true) => {
        if ((document.getElementById("SongInSiteAudio") as HTMLAudioElement).src !== DOMAIN + '/UserFiles/' + src) {
            (document.getElementById("SongInSiteAudio") as HTMLAudioElement).src = DOMAIN + '/UserFiles/' + src;
            if (increment) {
                fetch (DOMAIN + '/user/Music/IncrementEavesdroppers', {
                    method: 'POST',
                    headers: {
                        'Accept': 'application/json',
                        'Content-Type': 'application/json'
                    },
                    credentials: 'include',
                    body: JSON.stringify ({dir: src})
                }).then ( ()  => {})
            }
        }
        try {
            if (playingInSite) {
                // @ts-ignore
                document.getElementById(`ButtonPlayOFSongInSite${MusicStore.number - 1}`).style.display = "block";// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInSite${MusicStore.number - 1}`).style.display = "none";
            } else {
                // @ts-ignore
                document.getElementById(`ButtonPlayOFSongInPost${MusicStore.number}`).style.display = "block";// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInPost${MusicStore.number}`).style.display = "none";
            }
        } catch (e) {}
        if (MusicStore.number !== number + 1) {
            MusicStore.number = number + 1;
            MusicStore.src = src;
            MusicStore.playingInSite = true;
            MusicStore.songName = name;
            MusicStore.isRepeated = false
        }
        MusicStore.play(playingInSite);
    }),

    end: action( () => {
        if (MusicStore.array !== null && MusicStore.number !== null &&
          MusicStore.array.array[MusicStore.number - 1] != undefined && MusicStore.playingInSite && !MusicStore.isRepeated) {
            if (MusicStore.array.array[MusicStore.number - 1]) {
                MusicStore.isPlaying = true;
                MusicStore.number += 1;
                MusicStore.src = MusicStore.array.array[MusicStore.number - 1].dir;
                MusicStore.songName = MusicStore.array.array[MusicStore.number - 1].name;
                (document.getElementById("SongInSiteAudio") as HTMLAudioElement).play();
                return;
            }
        }
        MusicStore.isPlaying = false;
    }),

    pause: action( (inSite: boolean) => {
        (document.getElementById("SongInSiteAudio") as HTMLAudioElement).pause();
        try {
            if (inSite) {
                // @ts-ignore
                document.getElementById(`ButtonPlayOFSongInSite${MusicStore.number - 1}`).style.display = "block";// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInSite${MusicStore.number - 1}`).style.display = "none";
            } else {
                // @ts-ignore
                document.getElementById(`ButtonPlayOFSongInPost${MusicStore.number}`).style.display = "block";// @ts-ignore
                document.getElementById(`ButtonPauseOFSongInPost${MusicStore.number}`).style.display = "none";
            }
        } catch (e) {}

        MusicStore.isPlaying = false;
        if (MusicStore.number != MusicStore.number + 1) {
            MusicStore.isRepeated = false;
        }
    }),

    play: action( (inSite: boolean) => {
        (document.getElementById("SongInSiteAudio") as HTMLAudioElement).play().then(
            () => {
                try {
                    if (inSite) {
                        // @ts-ignore
                        document.getElementById(`ButtonPlayOFSongInSite${MusicStore.number - 1}`).style.display = "none";// @ts-ignore
                        document.getElementById(`ButtonPauseOFSongInSite${MusicStore.number - 1}`).style.display = "block";
                    } else {
                        // @ts-ignore
                        document.getElementById(`ButtonPlayOFSongInPost${MusicStore.number}`).style.display = "none";// @ts-ignore
                        document.getElementById(`ButtonPauseOFSongInPost${MusicStore.number}`).style.display = "block";
                    }
                } catch (e) {}
                runInAction( () => {MusicStore.isPlaying = true;})
            });
    }),

    addSongs: action( (object: {count: number, IsThereMore: boolean, array: {dir: string, date: string, name: string}[]}) => {
        MusicStore.array = object;
    }),

    setRepeated: action((value: boolean) => {
        MusicStore.isRepeated = value;
    }),

    setVolume: action((value: number) => {
        (document.getElementById("SongInSiteAudio") as HTMLAudioElement).volume = value;
        MusicStore.lastVolume = value;
    })

});