import {useState, useEffect, useRef, useCallback} from 'react';
import { observer } from 'mobx-react-lite';
import { ChatsStore } from '../../store/ChatsStore';
import {MessageStore} from "../../store/MessageStore";
import {UsersStore} from "../../store/UsersStore";
import {DOMAIN} from "../../helper/api";
import {Message} from "./UI/Message";
import styles from '../../styles/MessageBLock.module.scss'
import {SetLoading} from "../../helper/SetLoading";

//We need to get users avatars. There we get the avatars first time. Below we will check is some changed.
const getAvatars = new Map();

const getAvatarUrl = (userId: number) => {
    const cachedUrl = getAvatars.get(userId);
    if (cachedUrl) {
        return cachedUrl;
    } else {
        const user = UsersStore.users.find((user) => user.ID === userId);
        const avatar = user?.Avatar || 'NULL.png';
        const url = `${DOMAIN}/UserFiles/${userId}/Image/${avatar}`;
        getAvatars.set(userId, url);
        return url;
    }
};

export const MessageBox = observer(() => {

    const [isLoading, setIsLoading] = useState(false);
    const currentChat = ChatsStore.chats.find((chat) => chat.id === ChatsStore.whoIsActive);
    const currentMessageInfo = MessageStore.chats.find(chat => chat.chatkey === ChatsStore.whoIsActive);
    const startOfBox = useRef<HTMLDivElement>(null);
    const messageBoxRef = useRef<HTMLDivElement>(null);
    const observer = useRef<IntersectionObserver>(null);
    const scrollTo = useRef<{toTop: number, userScroll: number}>({userScroll: 0, toTop: 0});

    const downloadMessage = useCallback(async (e: any) => {
        if (!e[0].isIntersecting) return;
        if (currentChat.isGet) {return;}
        if (isLoading) return;
        setIsLoading(true);
        scrollTo.current = {
            toTop: messageBoxRef.current.scrollTop,
            userScroll: messageBoxRef.current.scrollHeight - messageBoxRef.current.scrollTop,
        }
        MessageStore.getMessages(ChatsStore.whoIsActive)
            .finally(() => {
                setIsLoading(false);
            });
    }, [ChatsStore.whoIsActive]);

    const createObserver = useCallback(() => {
        if (isLoading) return;
        if (observer.current) observer.current.disconnect();
        if (startOfBox.current) {
            observer.current = new IntersectionObserver(downloadMessage);
            observer.current.observe(startOfBox.current);
        }
    }, [ChatsStore.whoIsActive]);

    useEffect(() => {
        setIsLoading(true);
        if (!currentMessageInfo || !currentMessageInfo.messages) {
            setIsLoading(false);
            return;
        }
        if (currentMessageInfo.getUntil < 2) {
            MessageStore.getMessages(ChatsStore.whoIsActive)
                .finally(() => {
                    createObserver();
                    setIsLoading(false);
                    const box = messageBoxRef.current as HTMLDivElement;
                    box.scrollTop = box.scrollHeight;
                })
        } else {
            createObserver();
            setIsLoading(false);
            const box = messageBoxRef.current as HTMLDivElement;
            box.scrollTop = box.scrollHeight;
        }
    }, [ChatsStore.whoIsActive])

    useEffect(() => {
        const box = messageBoxRef.current as HTMLDivElement;
        const newScroll = box.scrollHeight - (scrollTo.current.userScroll + scrollTo.current.toTop);
        box.scrollTop = newScroll + scrollTo.current.toTop;
    }, [isLoading]);

    return (
        <div ref={messageBoxRef} className={styles.MessageBox}>
            <div
                ref={startOfBox}
                style={{ width: '1px', opacity: '0', height: '1px', position: 'relative', top: '100px' }}
            />
            {isLoading ? (
                <SetLoading styles={{ alignSelf: 'center', position: 'relative'}} />
            ) : (
                <>
                    {currentMessageInfo && currentMessageInfo.messages && currentMessageInfo.messages.length > 0
                        ?
                            currentMessageInfo.messages.map((value, index) => {
                                const lastAuthorIsIt = (() => {
                                    if (!currentChat.isConversation) return true;
                                    if (currentMessageInfo.messages[index - 1] == undefined) return false;
                                    return value.author === currentMessageInfo.messages[index - 1].author;
                                })();
                                return (
                                    <>
                                        <Message id={value.id} key={value.id} files={value.files} data={value.data} date={value.date}
                                                 author={value.author} avatar={getAvatarUrl(value.author)}
                                                 lastAuthorIsIt={lastAuthorIsIt}/>
                                    </>
                                )
                            })
                        :
                            <>
                                <div style={{display: 'flex', width: '100%', height: '100%', justifyContent: 'center', alignItems: 'center', fontSize: '20px', color: 'gray'}}>
                                    Тут ещё пусто, но вы можете это исправить.
                                </div>
                            </>
                    }
                </>
            )}
        </div>
    )
})