import React, {FC, memo, useCallback, useEffect, useRef, useState} from 'react';
import {Chat, ChatsStore} from "../../store/ChatsStore";
import {ChatLine} from "./ChatLine";
import {MessageStore} from "../../store/MessageStore";
import stylesCss from "../../styles/ChatsBlock.module.scss";
import {ListOfUsersForChangingChatList} from "./ListOfUsersForChangingChatList";
import { SetLoading } from '../../helper/SetLoading';
import {observer} from "mobx-react-lite";

interface Props {
    chats: Chat[] | number[];
    styles?: React.CSSProperties;
    isDefault?: boolean;
    name?: string;
}

export const Chats:FC<Props> = observer(({name, chats, styles, isDefault = true}) => {

    const [isChangingChatList, setIsChangingChatList] = useState(false);
    const chosenRef = useRef([]);
    const [i, setI] = useState(0);
    const lastChatLine = useRef<HTMLDivElement>(null);
    const observer = useRef<IntersectionObserver>();

    const callback = useCallback((e: any) => {
        if (!e[0].isIntersecting || chats[i*20 + 1] === undefined) return;
        MessageStore.getLastMessages(chats.slice(i*20, i*20 + 20).map(String))
            .then(() => {setI(i + 1)})
    }, [i]);

    const change = useCallback(() => {
        ChatsStore.ChangeChatsInChatList(name as string, chosenRef.current);
        setIsChangingChatList(false);
    }, []);

    useEffect(() => {
        if (!lastChatLine.current) return
        if (observer.current) observer.current.disconnect();

        observer.current = new IntersectionObserver(callback);
        observer.current.observe(lastChatLine.current as HTMLDivElement);
    }, [i, lastChatLine.current]);

    useEffect(() => {
        MessageStore.getLastMessages(chats.slice(i*20, i*20 + 20).map(String))
            .then(() => {setI(i + 1)})
    }, []);

    return (
        <div style={{...styles, maxHeight: '300px', overflowY: 'scroll', overflowX: 'hidden'}}>
            {isChangingChatList
                ?
                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', position: 'fixed', top: '0', left: '0',width: '100vw', height: '100vh'}}>
                        <div onClick={() => {setIsChangingChatList(false)}} style={{position: 'fixed', top: '0', left: '0', width: '100vw', height: '100vh', zIndex: '999', backgroundColor: 'rgba(0, 0, 0, .1)'}}/>
                        <div style={{maxHeight: '650px', overflowY: 'scroll', overflowX: 'hidden', zIndex: '1000'}}>
                            <div className={stylesCss.createNewChatListMenu}>
                                <div style={{fontSize: '20px'}}>{name === 'favourites' ? 'Важные' : name}</div>
                                <ListOfUsersForChangingChatList chats={chats} ref={chosenRef}/>
                                <div style={{display: 'flex', position: 'relative', margin: '5px 0', width: '100%', right: '0px'}}>
                                    <div className={stylesCss.button} style={{backgroundColor: '#fc2847', marginLeft: '138px'}} onClick={() => {setIsChangingChatList(false)}}>Отменить</div>
                                    <div className={stylesCss.button} style={{backgroundColor: '#00c77b'}} onClick={change}>Принять изменения</div>
                                </div>
                            </div>
                        </div>
                    </div>
                : <></>
            }
            {!isDefault || name === 'favourites'
                ?
                    <div onClick={() => {setIsChangingChatList(true)}}
                         style={chats.length > 0 ? {display: 'flex', justifyContent: 'center', alignItems: 'center', height: '42px', marginBottom: '5px',
                             width: '255px', position: 'relative',
                             left: '3px', backgroundColor: 'rgba(0, 0, 0, 0.1)', borderRadius: '8px', fontSize: '20px', color: 'grey', cursor: 'pointer'} : {display: 'flex', justifyContent: 'center', alignItems: 'center', height: '42px',
                             width: '255px', position: 'relative',
                             left: '3px', backgroundColor: 'rgba(0, 0, 0, 0.1)', borderRadius: '8px', fontSize: '20px', color: 'grey', cursor: 'pointer'}}
                    >
                        Изменить список
                    </div>
                : <></>
            }
            {i < 1
                ?  <SetLoading/>
                : typeof chats[0] === 'number'
                        ?  (chats as number[]).slice(0, i * 20 + 20).map((chat) => (<ChatLine chat={chat} key={chat}/>))
                        :  (chats as Chat[]).slice(0, i * 20 + 20).map((chat) => (<ChatLine chat={chat} key={chat.id}/>))
            }
            {i > 0 && <div ref={lastChatLine} style={{opacity: '0', position: 'relative', top: '-60px', height: '1px', width: '1px'}}/>}
        </div>
    );
});