
/*region Sort*/
import {promisify} from "util";

export const binarySort = <ArrayType extends any>(array: ArrayType[]): ArrayType[] => {
    if (array.length <= 1) {
        return array;
    }
    var pivot = array[0];

    var left = [];
    var right = [];
    for (var i = 1; i < array.length; i++) {
        array[i] < pivot ? left.push(array[i]) : right.push(array[i]);
    }
    return binarySort(left).concat(pivot, binarySort(right));
};

export const binarySortObject = <ArrayType extends Object>(array: ArrayType[], attributeName: string): ArrayType[] => {
    if (array.length <= 1) {
        return array;
    }

    var pivot = array[0];

    var left = [];
    var right = [];
    for (var i = 1; i < array.length; i++) {
        // @ts-ignore
        array[i][attributeName] < pivot[attributeName] ? left.push(array[i]) : right.push(array[i]);
    }
    return binarySortObject(left, attributeName).concat(pivot, binarySortObject(right, attributeName));
};

/*endregion*/

/*region Search*/

export const binarySearchSync = <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, returnIsFound = false): any => {
    var left = 0;
    var right = array.length;
    var found;
    while (left < right) {
        var middle = (left + right) >> 1;

        var compareResult = value > array[middle] ? 1 : value < array[middle] ? -1 : 0;
        if (compareResult > 0) {
            left = middle + 1;
        } else {
            right = middle;
            found = !compareResult;
        }
    }
    if (returnIsFound) {
        return {index: found ? left : ~left, found: found};
    }
    return found ? left : ~left;
};

export const binarySearchObjectSync = <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, attributeName: string, isReturningFound = false): number | {index: number, found: boolean} => {
    var left = 0;
    var right = array.length;
    var found;
    while (left < right) {
        var middle = (left + right) >> 1;

        var compareResult = value > array[middle][attributeName] ? 1 : value < array[middle][attributeName] ? -1 : 0;
        if (compareResult > 0) {
            left = middle + 1;
        } else {
            right = middle;
            found = !compareResult;
        }
    }
    if (isReturningFound) {
        return {
            index: found ? left : ~left,
            found: found == undefined ? false : found
        }
    }
    return found ? left : ~left;
};

export const binarySearch = async <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, returnIsFound = false): Promise<any> => {
    return new Promise( async (res) => {
        var left = 0;
        var right = array.length;
        var found;
        while (left < right) {
            var middle = (left + right) >> 1;

            var compareResult = value > array[middle] ? 1 : value < array[middle] ? -1 : 0;
            if (compareResult > 0) {
                left = middle + 1;
            } else {
                right = middle;
                found = !compareResult;
            }
        }
        if (returnIsFound) {
            return res({index: found ? left : ~left, found: found});
        }
        return res(found ? left : ~left);
    })
};

export const binarySearchObject = async <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, attributeName: string, isReturningFound = false): Promise<number | {index: number, found: boolean}> => {
    return new Promise( async (res) => {
        var left = 0;
        var right = array.length;
        var found;
        while (left < right) {
            var middle = (left + right) >> 1;

            var compareResult = value > array[middle][attributeName] ? 1 : value < array[middle][attributeName] ? -1 : 0;
            if (compareResult > 0) {
                left = middle + 1;
            } else {
                right = middle;
                found = !compareResult;
            }
        }
        if (isReturningFound) {
            return res({
                index: found ? left : ~left,
                found: found == undefined ? false : found
            })
        }
        return res(found ? left : ~left);
    })
};

/*endregion*/

/*region Insert*/

export const binaryInsertSync = <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType): boolean => {
    const index = binarySearchSync(array, value);
    if (index < 0) {
        array.splice(-(index + 1), 0, value);
        return true;
    }
    return false;
}

export const binaryInsertObjectSync = <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, attributeName: string): boolean => {
    //@ts-ignore
    const index = binarySearchObjectSync(array, value[attributeName], attributeName) as number;
    if (index < 0) {
        array.splice(-(index + 1), 0, value);
        return true;
    }
    return false;
};

export const binaryInsertObject = async <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType, attributeName: string): Promise<boolean> => {
    return new Promise(async (res) => {//@ts-ignore
        binarySearchObject(array, value[attributeName], attributeName).then( (index: number) => {
            if (index < 0) {
                array.splice(-(index + 1), 0, value);
                return res(true);
            }
            return res(false);
        })
    })
};

export const binaryInsert = async <ArrayType extends any[], ValueType>(array: ArrayType, value: ValueType): Promise<boolean> => {
    return new Promise(async (res) => {
        binarySearch(array, value).then( (index: number) => {
            if (index < 0) {
                array.splice(-(index + 1), 0, value);
                return res(true);
            }
            return res(false);
        })
    })
};

/*endregion*/