import {Eye} from "./eye";
import {handEye} from "./hand";

export const eye = new Eye();
handEye.eye = eye;
/** working only with Eye library.
 * @param willShow is u want to show that init() will be called
 */
export const init = (willShow: boolean) => {
	if (willShow) {
		eye.info('init infojs!');
	}
	console.error = (message: any) => {
		eye.error(message)
	}
	console.warn = (message: any) => {
		eye.warn(message);
		console.trace()
	}
}