import React, {memo, FC, useEffect, useRef, MouseEvent, useCallback} from 'react';
import styles from '../../Styles/style.module.scss';
import classes from "../../../../../styles/HeaderAndMusicPanelStyles.module.css";
import {ButtonsLine} from "./ButtonsLine";
import {VideoVolume} from "./VideoVolume";

export let IsVideoPlaying = false;
export const PlayVideo = () => {
    const Video = (document.getElementById('Video') as HTMLVideoElement);
    Video.play()
        .then( () => {
            (document.getElementById('Video_BtnPlay') as HTMLDivElement).style.display = 'none';
            (document.getElementById('Video_BtnPause') as HTMLDivElement).style.display = 'inline-block';
            (document.getElementById('Video_BtnPause') as HTMLDivElement).style.top = '7px';
            IsVideoPlaying = true;
            (document.getElementById('Video_BtnPause') as HTMLDivElement).style.height = '24px';
            if (Video.currentTime === Video.duration) Video.currentTime = 0;
            (document.getElementById('PauseDivInVideo') as HTMLDivElement).style.display = 'none';
        });
}
export const PauseVideo = () => {
    IsVideoPlaying = false;
    (document.getElementById('Video') as HTMLVideoElement).pause();
    (document.getElementById('Video_BtnPlay') as HTMLDivElement).style.display = 'inline-block';
    (document.getElementById('Video_BtnPause') as HTMLDivElement).style.display = 'none';
    (document.getElementById('PauseDivInVideo') as HTMLDivElement).style.display = 'block';
}

export const BottomController: FC = memo (() => {
    const ProgressLine = useRef<HTMLDivElement> (null);
    const ProgressBlock = useRef<HTMLDivElement> (null);

    useEffect (() => {
        const Video = (document.getElementById('Video') as HTMLVideoElement);
        Video.addEventListener('timeupdate', () => {
            (ProgressLine.current as HTMLDivElement).style.width = Video.currentTime / Video.duration * 100 + '%';
        });
        Video.addEventListener('ended', () => {
            PauseVideo();
        });
    });

    const TimeUpDateAfterClick = useCallback( (event: MouseEvent) => {
        const Video = (document.getElementById('Video') as HTMLVideoElement);
        const width = (ProgressBlock.current as HTMLDivElement).getBoundingClientRect().width, // Получаем точку клика относительно контейнера
            clickX=event.nativeEvent.offsetX, // Смещение клика относительно блока
            current_time=clickX/width,
            duration = Video.duration;
        Video.currentTime=Number( (current_time * duration).toFixed(2) );
    }, []);

    return (
        <div className={styles.BottomController} id={'Video_BottomController'}>
            <div className={styles.progressBlock} ref={ProgressBlock} onClick={TimeUpDateAfterClick}>
                <div className={styles.progressLine} ref={ProgressLine}/>
            </div>
            <div className={classes.BlockToButtonPlayOrPause}>
                <div className={classes.ButtonPlayOrPauseSong}>
                    <svg style={{display:'none', left: '10px', top: '7px'}} id={'Video_BtnPause'} className={classes.imgButtonPlayOrPause} onClick={PauseVideo} viewBox="30 30 100 100">
                        <g>
                            <path
                                d="M39.806,72.858h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.119c0-2.176,1.764-3.94,3.94-3.94h8.915   c2.176,0,3.94,1.764,3.94,3.94v37.799C43.746,71.094,41.982,72.858,39.806,72.858z"/>
                            <path
                                d="M68.109,72.821h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.082c0-2.176,1.764-3.94,3.94-3.94h8.915   c2.176,0,3.94,1.764,3.94,3.94v37.799C72.049,71.057,70.285,72.821,68.109,72.821z"/>
                            <path
                                d="M40.489,27.248c0.769,0.719,1.257,1.735,1.257,2.871v37.799c0,2.176-1.764,3.94-3.94,3.94h-8.915   c-0.234,0-0.46-0.03-0.683-0.069c0.704,0.658,1.643,1.069,2.683,1.069h8.915c2.176,0,3.94-1.764,3.94-3.94V31.119   C43.746,29.177,42.338,27.573,40.489,27.248z"/>
                            <path
                                d="M68.792,27.211c0.769,0.719,1.257,1.735,1.257,2.871v37.799c0,2.176-1.764,3.94-3.94,3.94h-8.915   c-0.234,0-0.46-0.03-0.683-0.069c0.704,0.658,1.643,1.069,2.683,1.069h8.915c2.176,0,3.94-1.764,3.94-3.94V31.082   C72.049,29.14,70.641,27.535,68.792,27.211z"/>
                            <path
                                d="M39.806,72.858h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.119   c0-2.176,1.764-3.94,3.94-3.94h8.915c2.176,0,3.94,1.764,3.94,3.94v37.799C43.746,71.094,41.982,72.858,39.806,72.858z"
                                style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                            <path
                                d="M68.109,72.821h-8.915c-2.176,0-3.94-1.764-3.94-3.94V31.082   c0-2.176,1.764-3.94,3.94-3.94h8.915c2.176,0,3.94,1.764,3.94,3.94v37.799C72.049,71.057,70.285,72.821,68.109,72.821z"
                                style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                        </g>
                    </svg>
                    <svg viewBox="0 0 80 80" id={'Video_BtnPlay'} className={classes.imgButtonPlayOrPause} style={{width:"30px", height:"24px", top:"-3px"}} onClick={PlayVideo}>
                        <g>
                            <path
                                d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7   C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                            <path
                                d="M69.981,47.977l-38.625-22.3c-0.233-0.134-0.474-0.21-0.716-0.259l37.341,21.559c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-0.349,0.201-0.716,0.288-1.078,0.301c0.656,0.938,1.961,1.343,3.078,0.699l38.625-22.3   C71.538,51.124,71.538,48.876,69.981,47.977z"/>
                            <path
                                d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7C27.852,25.902,29.798,24.778,31.356,25.677z"
                                style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}}/>
                        </g>
                    </svg>
                </div>
            </div>
            <div id={'PauseDivInVideo'} style={{position: 'fixed', left: 'calc(50% - 75px)', top: 'calc(50% - 75px)', borderRadius: '6px',
                padding: '10px', backgroundColor: 'rgba(0, 0, 0, .4)'}} onClick={PlayVideo}>
                <svg viewBox="0 0 100 100" height={'140px'}>
                    <g>
                        <path d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7   C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                        <path d="M69.981,47.977l-38.625-22.3c-0.233-0.134-0.474-0.21-0.716-0.259l37.341,21.559c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-0.349,0.201-0.716,0.288-1.078,0.301c0.656,0.938,1.961,1.343,3.078,0.699l38.625-22.3   C71.538,51.124,71.538,48.876,69.981,47.977z"/>
                        <path style={{fill: '#ffffff', stroke: '#ffffff', strokeMiterlimit: '10'}} d="M31.356,25.677l38.625,22.3c1.557,0.899,1.557,3.147,0,4.046   l-38.625,22.3c-1.557,0.899-3.504-0.225-3.504-2.023V27.7C27.852,25.902,29.798,24.778,31.356,25.677z"/>
                    </g>
                </svg>
            </div>
            <VideoVolume/>
            <ButtonsLine/>
        </div>
    );

});