import React, {FC, useState} from 'react';
import {observer} from "mobx-react-lite";
import {Chat} from "../../store/ChatsStore";
import {SearchLine} from "./SearchLine";

interface Props {
	setFoundArray: (chats: Chat[]) => void;
	setIsSearching: (bool: boolean) => void;
	setIsChatCreating: (bool: boolean) => void;
}

export const CreatingOrSearchChatWindow:FC<Props> = observer(({setFoundArray, setIsSearching, setIsChatCreating}) => {

	const [isOpened, setIsOpened] = useState(false);

	if (isOpened) {
		return (
			<>
				<SearchLine setFoundArray={setFoundArray}/>
				<div
					onClick={() => {setIsChatCreating(true)}}
					style={{
						marginTop: '3px',
						display: 'flex',
						justifyContent: 'center',
						alignItems: 'center',
						textAlign: 'center',
						padding: '6px 0',
						width: '262px',
						marginBottom: '5px',
						backgroundColor: 'rgba(0, 0, 0, 0.1)',
						borderRadius: '5px',
						fontSize: '20px',
						color: 'grey',
						cursor: 'pointer',
					}}
				>Начать новую переписку</div>
			</>
		);
	} else {
		return (
			<div
				onClick={() => {setIsOpened(true);setIsSearching(true)}}
				style={{
					marginTop: '3px',
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
					textAlign: 'center',
					padding: '4px 0',
					width: '262px',
					marginBottom: '5px',
					backgroundColor: 'rgba(0, 0, 0, 0.1)',
					borderRadius: '5px',
					fontSize: '20px',
					color: 'grey',
					cursor: 'pointer',
				}}
			>
				Найти переписку или начать новую
			</div>
		)
	}
});