import React, {memo, FC, useRef, useEffect, useCallback} from 'react';
import styles from '../../Styles/style.module.scss';
import GlobalStyles from '../../../../styles/GlobalStyles.module.scss';

type ProbableSVG = 'speed' | 'close' | 'repeat';

export const ButtonInLine: FC<{Text: string, svg: ProbableSVG, onClick: any}> = memo (({Text, svg, onClick}) => {

    const Message = useRef<HTMLDivElement>(null);
    useEffect (() => {
        (Message.current as HTMLDivElement).style.display = 'none';
        (Message.current as HTMLDivElement).style.width = svg === 'speed' ? '149px' : '153px';
        (Message.current as HTMLDivElement).style.left = svg === 'speed' ? '-96px' : '-100px';
    });
    const onHover = useCallback (() => {
        (Message.current as HTMLDivElement).style.display = 'block';
    }, []);
    const onLeave = useCallback (() => {
        (Message.current as HTMLDivElement).style.display = 'none';
    }, []);

    return (
        <div onClick={onClick} style={{display: 'inline-block', width: '25px', height: '25px'}} onMouseEnter={onHover} onMouseLeave={onLeave}>
            <div ref={Message} className={styles.message}>
                {Text}
            </div>
            {svg === 'speed'
                ?
                    <svg width={25} viewBox="0 0 64 64">
                        <g>
                            <path fill={'#fff'} d="M28.371,31.879c0,2.19,1.777,3.966,3.966,3.966c1.538,0,2.856-0.884,3.514-2.163l0.011,0.01l7.24-13.062l-12.71,7.794   l0.012,0.012C29.197,29.117,28.371,30.395,28.371,31.879z"/>
                            <path fill={'#fff'} d="M48.315,12.981C44.251,9.429,39,7.161,33,6.822v6.027c5,0.318,7.946,1.906,10.904,4.384L48.315,12.981z"/>
                            <path fill={'#fff'} d="M51.146,30h6.02c-0.404-6-2.751-10.93-6.395-14.97l-4.259,4.233C49.078,22.203,50.766,26,51.146,30z"/>
                            <path fill={'#fff'} d="M51.174,33c-0.637,10-8.922,17.931-19.042,17.931c-10.535,0-19.421-8.544-19.421-19.078C12.711,21.825,21,13.62,30,12.85   V6.823C17,7.602,6.711,18.54,6.711,31.879c0,13.843,11.42,25.052,25.263,25.052C45.406,56.931,56.564,46,57.205,33H51.174z"/>
                        </g>
                    </svg>

                :
                    svg === 'close'
                        ?
                            <div
                                className={GlobalStyles.noselect}
                                style={{
                                    zIndex: '1003',
                                    position: 'absolute',
                                    display: 'inline-block',
                                    color: 'white',
                                    width: '25px',
                                    height: '25px',
                                    top: '-12px',
                                    left: '40px',
                                    fontSize: '42px',
                                    cursor: 'pointer'
                                }}
                            >
                                ×
                            </div>

                        :
                            <svg width="25px" height="25px" viewBox="0 0 430 430">
                                <g>
                                    <g>
                                        <path d="M296.924,403.243c-22.656,10.875-47.5,17.813-73.734,19.813v-64.813c14.609-1.625,28.672-4.906,41.609-10.594
                                    L296.924,403.243z" fill={'#fff'}/>
                                        <path d="M64.814,199.868H0.001c8.219-107.219,97.906-192,207.188-192c64.297,0,124.735,30.094,163.797,79.969l59.812-34.563
                                    l0.125,197.844l-171.406-98.813l55.094-31.875c-27.062-30.313-65.999-48.563-107.421-48.563
                                    C133.251,71.868,72.876,128.055,64.814,199.868z" fill={'#fff'}/>
                                        <path d="M322.674,301.024l56,32.312c-14.531,21.156-32.875,39.47-54,54.031l-32.314-56
                                    C303.924,322.805,314.111,312.586,322.674,301.024z" fill={'#fff'}/>
                                        <path d="M191.189,358.243v64.813c-26.25-2-51.094-8.938-73.719-19.813l32.094-55.594
                                    C162.533,353.336,176.595,356.618,191.189,358.243z" fill={'#fff'}/>
                                        <path d="M122.033,331.368l-32.313,56c-21.156-14.562-39.5-32.875-54.031-54.031l56-32.312
                                    C100.251,312.586,110.439,322.805,122.033,331.368z" fill={'#fff'}/>
                                        <path
                                            d="M75.376,273.461l-55.563,32.094C8.938,282.93,2,258.118,0,231.868h64.813C66.439,246.461,69.72,260.524,75.376,273.461z"
                                            fill={'#fff'}/>
                                    </g>
                                </g>
                            </svg>
            }
        </div>
    );

});