import React, {FC} from "react";
import {SpeedButton} from "./SpeedButton";

const SpeedArray = [0.25, 0.5, 0.75, 1, 1.25, 1.5, 2, 3, 4];

export const SpeedLine:FC<{index: number, Function: any}> = ({index, Function}) => {

    return (
        <div>
            <SpeedButton Function={Function} number={SpeedArray[index * 3]}/>
            <SpeedButton Function={Function} number={SpeedArray[1 + index * 3]}/>
            <SpeedButton Function={Function} number={SpeedArray[2 + index * 3]}/>
        </div>
    )
}