import React, {memo, FC, useCallback, useState} from 'react';
import styles from '../../Styles/style.module.scss';
import GlobalStyles from  '../../../../styles/GlobalStyles.module.scss';
import {ButtonInLine} from "./ButtonInLine";
import {SpeedLine} from "./SpeedLine";

export const ButtonsLine: FC = memo (() => {

    const [IsSpeedMenuVision, setIsSpeedMenuVision] = useState (false);
    
    const ChangeSpeed = useCallback (() => {
        setIsSpeedMenuVision(!IsSpeedMenuVision);
    }, [IsSpeedMenuVision]);

    const Exit = useCallback (() => {
        document.exitFullscreen();
    }, []);

    return (
        <div className={styles.ButtonsLine} style={{userSelect: 'none'}}>
            <div style={{...IsSpeedMenuVision ? {display: 'block'} : {display: 'none'},
                position: 'absolute', top: '-128px', width: '180px', height: '100px', left: '-95px' } }>
                <div className={GlobalStyles.ModalWindow} onClick={ChangeSpeed}></div>
                <div style={{zIndex: '1003', position: 'absolute'}}>
                    <SpeedLine Function={ChangeSpeed} index={0}/>
                    <SpeedLine Function={ChangeSpeed} index={1}/>
                    <SpeedLine Function={ChangeSpeed} index={2}/>
                </div>
            </div>
            <ButtonInLine
                svg={'speed'}
                Text={'Изменить скорость'}
                onClick={ChangeSpeed}
            />
            <ButtonInLine
                svg={'close'}
                Text={'Закрыть окно видео'}
                onClick={Exit}
            />
        </div>
    );

});