import {action, observable} from "mobx";

interface VideoStoreInterface {
    src: string
    isOpen: boolean;
    name: string;
    preferSpeed: number;
    preferVolume: number;
    currentTime: number;

    readonly openNewVideo: (name: string, src: string) => void;
    readonly close: (currentTime: number, preferSpeed: number, preferVolume: number) => void;
}

export const VideoStore: VideoStoreInterface = observable({
    src: '',
    isOpen: false,
    name: '',
    preferSpeed: 1,
    preferVolume: 0.5,
    currentTime: 0,

    openNewVideo: action((name: string, src: string) => {
        if (VideoStore.src !== src) {
            VideoStore.src = src;
            VideoStore.name = name;
        }
        VideoStore.isOpen = true
    }),

    close: action((currentTime: number, preferSpeed: number, preferVolume: number) => {
        VideoStore.currentTime = currentTime;
        VideoStore.preferVolume = preferSpeed;
        VideoStore.preferSpeed = preferSpeed;
        VideoStore.isOpen = false;
    })
});