import React, {FC, memo} from "react";
import BtnPlayOrPause from "./BtnPlayOrPause";
import classes from "./../../../styles/HeaderAndMusicPanelStyles.module.css";

const MusicLine:FC<{counter: number, MusicLineData: any}> = memo((probs) => {

    return (
        <div className={classes.MusicListMusicDiv}>
            <div className={classes.MusicListMusicLine}>
                <BtnPlayOrPause
                     id = {probs.counter - 1}
                />
                <div className={classes.SongNameInMusicListMusicLine}>{probs.MusicLineData['name']}</div>
            </div>
        </div>
    );
});

export default MusicLine;