import {SetNotification} from "./SetNotification";
import {AllowImageTypes, AllowMusicTypes, AllowVideoTypes} from "./FilesType";
import {RestApiMethods} from "./hand";
import {eye} from "./infojs";
import {DOMAIN} from "./api";

const uploadFile = async (file: File) => {
    const path = "/api/file/upload";
    const formData = new FormData();
    formData.append('file', file)
    const start = Date.now();
    eye.fetchSend(path, RestApiMethods.Post)
    return  fetch(DOMAIN + path, {
        body: formData,
        method: "POST",
        credentials: 'include',
    })
        .then(async Res => {
            eye.fetchGet(path, RestApiMethods.Post, Res.status, Date.now() - start)
            if (Res.status === 200) {
                return Res.text();
            } else {
                switch ((await Res.json() as {message: string}).message) {
                    case 'file too large': {
                        SetNotification('Файл слишком большой!');
                        throw new Error(await Res.json());
                    }

                    default: {
                        throw new Error(await Res.json());
                    }
                }
            }
        })
}

/**
 * This function upload file in server and return type of file and file`s name or return null if file has no type
 * @param file casual File
 * @constructor
 */
export const UploadFile = async (file: File): Promise<{ src: string, type: 'Music' | 'Video' | 'Image' | 'Other' }> => {
    const FileType = file.name.split('.').pop();
    if (FileType !== undefined ) {
        let name: string;
        const query = uploadFile(file);
        await query
            .then(res => name = res)
            .catch((reason) => {throw new Error(reason)});
        console.log(name)
        if (AllowMusicTypes.indexOf(FileType) !== -1) {
            return { src: name, type: 'Music' };
        } else {
            if (AllowVideoTypes.indexOf(FileType) !== -1) {
                return { src: name, type: 'Video' };
            } else {
                if (AllowImageTypes.indexOf(FileType) !== -1) {
                    return { src: name, type: 'Image' };
                } else {
                    return { src: name, type: 'Other' };
                }
            }
        }
    } else {
        SetNotification('Неправильный формат файла');
        return null;
    }
}
