import React from 'react';
import ReactDOM from 'react-dom/client';
import {MainLayOut} from "./LayOut/MainLayOut";
import {ChatsStore} from "./store/ChatsStore";
import {MainPart} from "./Components/MainPart";
import {Connection} from "./Connection";
import {initFastArrays} from "./libs/fastjs";
import {init as initInfoJS} from "./helper/infojs";
import {init as initHand} from "./store/config";

initInfoJS(true);
initHand()
initFastArrays(false);
export const USERID = localStorage.getItem('userId');
/**
 * The logic of this function is simple. In first, we get chats and all users. Then we render the page. This page haves rendered lines of chat. When a user sees first from 20 lines of chat, we get the latest messages for them. When a user clicks on lines of chat we get messages and render them.
 * */
const start = async () => {
    /**
     * We get chats and users. So after this function we can render for the first time.
     * But this won`t have the latest messages or message in general.
     * */
    await ChatsStore.getReady();

    (document.getElementById('PrevLoad') as HTMLElement).remove();
    ReactDOM.createRoot( (document.getElementById ("root") as HTMLElement) ).render (
        <MainLayOut>
            <MainPart/>
        </MainLayOut>
    );
}

if (Connection) {
    start();
} else {
    const cb = () => {
        if (Connection != undefined) start();
        else interval = setTimeout(cb, 500)
    }
    let interval: any;
    setTimeout(cb, 500)
}

