import React, {memo, FC, useCallback} from 'react';
import {Video} from "./Video";
import {TopController} from "./UI/TopController";
import {BottomController} from "./UI/BottomController";

let IsShowingVideoInterface = true;

export const VideoPlayer: FC = memo (() => {

    const ToOnMouseMove_CB = useCallback( () => {
        (document.getElementById('Video_BottomController') as HTMLDivElement).style.display = 'none';
        (document.getElementById('Video_TopController') as HTMLDivElement).style.display = 'none';
        (document.getElementById('VideoPlayer') as HTMLDivElement).style.cursor = 'none';
        IsShowingVideoInterface = false;
    }, []);

    let ToOnMouseMove: Function;

    const onMouseMove = useCallback( () => {
        if (!IsShowingVideoInterface) {
            (document.getElementById('Video_BottomController') as HTMLDivElement).style.display = 'block';
            (document.getElementById('VideoPlayer') as HTMLDivElement).style.cursor = 'pointer';
            (document.getElementById('Video_TopController') as HTMLDivElement).style.display = 'block';
        }
        IsShowingVideoInterface = true;
        // @ts-ignore
        clearTimeout(ToOnMouseMove);// @ts-ignore
        ToOnMouseMove = setTimeout(ToOnMouseMove_CB, 2000);
    }, [IsShowingVideoInterface]);

    return (
        <div onMouseMove={onMouseMove}>
            <TopController/>
            <Video/>
            <BottomController/>
        </div>
    );

});