import React, {FC, memo, useEffect, useRef, useState} from 'react';
import styles from '../../styles/ChatsBlock.module.scss';
import {Chat, ChatsStore} from "../../store/ChatsStore";
import {binarySearchObjectSync, binarySearchSync} from "../../helper/binaryOperation";
import {ChatLineToCreating} from "./ChatLineToCreating";
import {GetAvatarSrc} from "../../functions/GetAvatarSrc";
import {USERID} from "../../index";

interface Props {
    name: string;
    chats: (number | Chat)[];
    Chosen: number[];
    SetChosen: (value: number) => void;
    defaultOpen?: boolean;
}

export const ChatListToCreating:FC<Props> = memo(({chats, name, Chosen, SetChosen, defaultOpen}) => {

    const [isOpen, setIsOpen] = useState(defaultOpen == undefined ? false : defaultOpen);
    const [i, setI] = useState(0), currentI = {current: i};
    const observer1 = useRef<IntersectionObserver>();
    const LastElem = useRef(null), block = useRef<HTMLDivElement>(null);

    useEffect( () => {
        if (chats.length < 21 || LastElem.current == null || chats[(i + 1) * 20 + 1] == undefined) return;
        if (observer1.current) observer1.current.disconnect();
        // @ts-ignore
        observer1.current = new IntersectionObserver((entries: any) => {
            if (!entries[0].isIntersecting) return;
            setI(currentI.current + 1);
            currentI.current = i + 1;
        });
        // @ts-ignore
        observer1.current.observe(LastElem.current);
    }, [i, LastElem.current, isOpen]);//TODO прокрутка вниз убирает 20 элементов, прокрутка вверх ( ещё не готова ) добавляет 20 элементов вверх

    return (
        <div className={styles.ChatList} style={{width: '359px', marginBottom: '0'}}>
            <div className={styles.name} style={{width: '359px'}} onClick={isOpen ? () => {setI(0);currentI.current = 0;setIsOpen(false)} : () => {setIsOpen(true)}}>
                <div className={styles.text} style={{width: '314px'}}>
                    {name}
                </div>
                <svg viewBox="0 0 24 24" style={{fill: 'rgba(0, 0, 0, .1)', cursor: "pointer", width: '32px',marginRight: '5px',transform: isOpen ? 'rotate(0)' : 'rotate(180deg)'}}>
                    <path d="m4 12 1.41 1.41L11 7.83V20h2V7.83l5.58 5.59L20 12l-8-8-8 8z"></path>
                </svg>
            </div>
            {isOpen
                ?
                    <div style={{maxHeight: '200px', overflowX: 'hidden', overflowY: 'scroll'}} ref={block}>
                        {
                            chats.slice(0, (i + 1) * 20).map((value) => {
                                let Chat: Chat;
                                if (typeof value === 'number') {
                                    if (value === +USERID) {
                                        return;
                                    }
                                    const index = binarySearchObjectSync(ChatsStore.chats, value, 'id', true) as {found: boolean, index: number};
                                    if (!index.found) return;
                                    Chat = ChatsStore.chats[index.index];
                                } else {
                                    Chat = value;
                                }
                                const avatarSrc: string = (() => GetAvatarSrc(Chat))();
                                const IsItChosen = binarySearchSync(Chosen, Chat.id, true).found == true;
                                return (
                                    <ChatLineToCreating key={Chat.id} Chat={Chat} SetChosen={() => {SetChosen(Chat.id)}} name={Chat.name as string} avatarSrc={avatarSrc} Chosen={IsItChosen}/>
                                );
                            })
                        }
                        <div ref={LastElem} style={{position: 'relative', bottom: '100px', height: '1px'}}/>
                    </div>
                :
                    <></>
            }
        </div>
    );
});