import React, {FC} from 'react';
import {User, UsersStore} from "../../../store/UsersStore";
import {observer} from "mobx-react-lite";
import {Checkbox} from "@mui/material";
import {DOMAIN} from "../../../helper/api";
import {LazyImg} from "../../../helper/LazyImg";
import {ChatsStore} from "../../../store/ChatsStore";
import {binarySearch, binarySearchSync} from "../../../helper/binaryOperation";
import {Loading} from "../../../helper/Loading";
import {USERID} from "../../../index";

interface Props {
    users: User[];
    members: number[];
    setIsMember: (value: number) => void;
    title: string;
    isMembersBlock: boolean;
}

export const UsersToEditMembersBlock:FC<Props> = observer(({users, members, title, setIsMember, isMembersBlock}) => {

    const array: User[] = [];
    if (users.length === 0) {
        return <></>;
    }
    array.push(...users);

    return (
        <div style={{width: '338px', overflow: 'hidden', marginBottom: '5px', padding: '2px',
            display: 'flex', flexFlow: 'column', borderRadius: '4px', border: '1px solid rgba(0, 0, 0, .1)'}}>
            <div style={{alignSelf: 'center'}}>{title}</div>
            {
                array.map((value, index) => {
                    const {found} = binarySearchSync(UsersStore.onlineUsers, value.ID, true) as {index: number, found: boolean};
                    const isOnline = found || value.ID === +USERID;
                    const avatar = (() => {
                        if (value.Avatar != null && value.Avatar != '') {
                            return DOMAIN + '/UserFiles/' + value.ID + '/Image/' + value.Avatar;
                        } else {
                            return DOMAIN + '/UserFiles/NULL.png';
                        }
                    })();
                    return (
                        <div key={value.ID} style={{display: 'flex', justifyContent: 'space-between', borderTop: '1px solid rgba(0, 0, 0, .1)', padding: '2px 0'}}>
                            <div style={{display: 'flex', alignItems: 'center'}}>
                                <LazyImg src={avatar} ImageStyles={isOnline ? {width: '38px', height: '40px', borderRadius: '50%'} : {width: '40px', height: '40px', borderRadius: '50%'}}
                                         DivToImageStyles={isOnline ? {marginRight: '3px', border: 'solid rgb(75, 179, 115) 2px', borderRadius: '50%', width: '38px', height: '38px'} : {marginRight: '3px'}}
                                         element={<Loading/>}/>
                                <div style={{width: '250px', overflow: 'hidden', textAlign: 'left'}}>{value.Surname} {value.Name}</div>
                            </div>
                            {!isMembersBlock || index > 0 ? <Checkbox checked={members.indexOf(value.ID) != -1} onClick={() => {setIsMember(value.ID)}}/> : <></>}
                        </div>
                    )
                })
            }
        </div>
    );
});