import React, {forwardRef, useCallback, useRef, useState} from 'react';
import {AlreadyInput} from "../../helper/AlreadyInput";
import styles from "../../styles/ChatsBlock.module.scss";
import {observer} from "mobx-react-lite";
import {ChatListToCreating} from "./ChatListToCreating";
import {Chat, ChatsStore} from "../../store/ChatsStore";
import {binarySearchSync} from "../../helper/binaryOperation";

export const ListOfUsersForChangingChatList = observer(forwardRef<any, {chats: (number | Chat)[]}>(({chats}, ref) => {

    const foundArray = useState<Chat[]>([]);
    const [chosen, setChosen] = useState<number[]>(chats.map(value => typeof value === "number" ? value : value.id));
    const deferredCallback = useRef<any>();
    const choose = useCallback((number: number) => {
        const {found, index} = binarySearchSync(chosen, number, true);
        if (!found) chosen.splice(-(index + 1), 0, number); else chosen.splice(index, 1);
        setChosen([...chosen]);
        // @ts-ignore
        ref.current = chosen;
    }, [chosen]);
    const find = useCallback((e: any) => {
        const value = e.target.value, array:Chat[] = [];
        if (value === '') {
            if (deferredCallback.current) clearTimeout(deferredCallback.current);
            foundArray[1]([]);
            return;
        }
        if (deferredCallback.current) clearTimeout(deferredCallback.current);
        deferredCallback.current = setTimeout(() => {
            for (let i = 0; i < ChatsStore.chats.length; i++) {
                if ((ChatsStore.chats[i].name as string).toLowerCase().indexOf(value) != -1) array.push(ChatsStore.chats[i])
            }
            foundArray[1](array);
        }, 1500)
    }, []);

    if (ChatsStore.chats.length > 0) {
        return (
            <div className={styles.ListOfUsersForCreatingListOfChat}>
                <AlreadyInput width={'352px'} reg={'all'} length={120} design={'default'} label={'Найти нужный чат'} onEnter={find} onChange={find}/>
                <div className={styles.ChatList} style={{width: '360px', padding: '5px', marginBottom: '0', alignItems: 'normal'}}>
                    {foundArray[0].length > 0
                        ? <><ChatListToCreating Chosen={chosen} defaultOpen={true} SetChosen={choose} name={'Найденные поиском'} chats={foundArray[0]}/><div style={{height: '5px'}}/></>
                        : <></>
                    }
                    {chats.length > 0
                        ? <><ChatListToCreating Chosen={chosen} SetChosen={choose} name={'Принадлежащие этому списку чаты'} chats={chats} defaultOpen={true}/><div style={{height: '5px'}}/></>
                        : <></>
                    }
                    {ChatsStore.chatsList[0].chats.length > 0
                        ? <><ChatListToCreating Chosen={chosen} SetChosen={choose} name={'Важные'} chats={ChatsStore.chatsList[0].chats}/><div style={{height: '5px'}}/></>
                        : <></>
                    }
                    {ChatsStore.chatsList[1].chats.length > 0
                        ? <><ChatListToCreating Chosen={chosen} SetChosen={choose} name={'Друзья'} chats={ChatsStore.chatsList[1].chats}/><div style={{height: '5px'}}/></>
                        : <></>
                    }
                    {ChatsStore.chatsList[2].chats.length > 0
                        ? <><ChatListToCreating Chosen={chosen} SetChosen={choose} name={'Подписчики'} chats={ChatsStore.chatsList[2].chats}/><div style={{height: '5px'}}/></>
                        : <></>
                    }
                    <ChatListToCreating Chosen={chosen} SetChosen={choose} name={'Все'} chats={ChatsStore.chats}/>
                </div>
            </div>
        );
    }
    return <></>;

}));