import React, {FC, useEffect} from 'react';
import styles from './Styles/style.module.scss'
import {VideoPlayer} from "./FC/VideoPlayer";
import {VideoStore} from "../../store/stores/VideoStore";
import {observer} from "mobx-react-lite";

let NowVideoSrc: any = null;

export const VideoWindow: FC = observer(() => {

    useEffect (() => {
        if (NowVideoSrc === null || NowVideoSrc === '') {
            NowVideoSrc = VideoStore.src;
        }
        if (VideoStore.isOpen && VideoStore.src === NowVideoSrc) {
            (document.getElementById('Video') as HTMLVideoElement).currentTime = VideoStore.currentTime;
        }
    });

    if (VideoStore.isOpen) {
        return (
            <div className={styles.VideoWindow} id={'VideoPlayer'} style={{cursor: 'pointer'}}>
                <VideoPlayer/>
            </div>
        );
    }


    return <></>;

});