import ButtonPlayOrPause from "./AI/ButtonPlayOrPause";
import ButtonRepeat from "./AI/ButtonRepeat"
import VolumeSign from "./AI/VolumeSign"
import classes from "./../../styles/HeaderAndMusicPanelStyles.module.css"
import React, {FC} from "react";
import {MusicStore} from "../store/stores/MusicStore";
import {observer} from "mobx-react-lite";

const MusicLineInSiteMusic:FC<{ShowMusicList: any}> = observer((probs) => {

    function SetCurrentTime (e:React.MouseEvent<HTMLDivElement>) {
        e.stopPropagation();
        let width = (document.getElementById('SongInSiteProgressContainer') as HTMLDivElement).getBoundingClientRect().width, // Получаем точку клика относительно контейнера
         clickX=e.nativeEvent.offsetX, // Смещение клика относительно блока
         current_time=clickX/width,
         duration = (document.getElementById('SongInSiteAudio') as HTMLAudioElement).duration;

        (document.getElementById('SongInSiteAudio') as HTMLAudioElement).currentTime=Number( (current_time * duration).toFixed(2));
    }

    if (MusicStore.songName === '') {
        try {
            (document.getElementById('MusicInSiteTopPanel') as HTMLDivElement).style.top = "2px";
        } catch (error) {}
        return (//Ниже я опишу два странных дива они никогда не будут использоваться, они только оберегают код от ошибок тем, что существуют
            <div className={classes.MusicLineInSiteMusic} onClick={probs.ShowMusicList}>
                <div className={classes.MusicNameInLineInSiteMusic.Empty}>
                    <div className={classes.MusicNameInLineInSiteMusicInfinitiveLineEmpty} id="MusicLineInSiteMusic">Введите название песни</div>
                <div data-comment="NEVERTOUCHTHIS" id="SongInSiteProgressContainer" style={{display:"none"}}><div data-coment="NEVERTOUCHTHIS2" id="SongInSiteProgressBar" style={{display:"none"}}></div></div>
                </div>
            </div>
        )
    } else {
        try {
            (document.getElementById('MusicInSiteTopPanel') as HTMLDivElement).style.top = "6px";
        } catch (error) {}
        return (
            <div className={classes.MusicLineInSiteMusic} onClick={probs.ShowMusicList}>
                <div style={{display: "inline-block"}} onClick={(event) => {event.stopPropagation();} }>
                    <ButtonPlayOrPause/>
                </div>
                <div className={classes.MusicNameInLineInSiteMusic} >
                    <div
                     className={classes.MusicNameInLineInSiteMusicInfinitiveLine}
                     id="MusicLineInSiteMusic"
                     placeholder="Введите название песни">
                        {MusicStore.songName}
                    </div>
                </div>
                <div style={{display: "inline-block"}} onClick={(event) => {event.stopPropagation();} }>
                    <ButtonRepeat/>
                </div>
                <div style={{display:"inline-block"}}>
                    <VolumeSign/>
                </div>
                <div id="SongInSiteProgressContainer" onClick={(e) => {SetCurrentTime(e)}} className={classes.SongInSiteProgressContainer}>
                    <div id="SongInSiteProgressBar" className={classes.SongInSiteProgressBar}></div>
                </div>
            </div>
        );
    }
})
    

export default MusicLineInSiteMusic;